import { ScriptLang } from "../interfaces/Scripting/scriptLang";
import { ICompJson } from "../interfaces/ComponentInterfaces/ICompJson";
import { TypeHelper } from "./typeHelper";

export interface ICompLocation {
    compJson: ICompJson,
    curPath: Array<string | number>
}

export interface ICompParentLocation {
    selfIndex: number | undefined,
    parentArray: Array<ICompJson | null>,
    parent: ICompJson | null
}

export enum CompSearchKey {
    PID,
    EditorID,
    Render
}
export class QJsonHelper {
    /**
     * Make the calculation with given pageJson
     * @param pageJson Given pageJson
     * @param callBack Function to be handled. Return false to STOP traversing.
     * @returns boolean
     */
    public static ForEachComp(
        pageJson: Array<ICompJson>,
        callBack: (compJson: ICompJson, path: Array<string | number>, info?: { compName: string, eID: string, parentCompJson?: ICompJson }) => boolean
    ) {
        var traverse = function (curJson: Array<ICompJson | null>, path: Array<string | number>, parentCompJson?: ICompJson) {
            for (var i = 0; i < curJson.length; i++) {
                const compJson = curJson[i];
                if (!compJson) {
                    continue;
                }

                path.push(i);

                if (callBack(compJson, path.map(item => item), { compName: compJson.T, eID: compJson._Editor.eID, parentCompJson }) === false) {
                    return false;
                }

                if (compJson.C) {
                    for (var childNamed in compJson.C) {
                        path.push(childNamed);
                        if (traverse(compJson.C[childNamed].c, path, compJson) === false) {
                            return false;
                        }
                        path.pop();
                    }
                }

                path.pop();
            }
        }

        traverse(pageJson, []);
    }
    
    /**
     * @deprecated Please use QJsonHelper.FindParentArrayPath
     */
    public findParentArrayPath(path: (string | number)[], pageJson: Array<ICompJson>): ICompParentLocation{
        return QJsonHelper.FindParentArrayPath(path, pageJson);
    }
    
    /**
     * Find json and path with given path in the given pageJson
     * @param path Sample path: [3,default,2] 
     * @param pageJson Given pageJson
     * @returns ICompParentLocation
     */
    public static FindParentArrayPath(path: (string | number)[], pageJson: Array<ICompJson>): ICompParentLocation {
        let parent: ICompJson | null = null;
        const selfIndex = <number>path.pop();
        if (path.length == 0) {
            return { selfIndex, parentArray: pageJson, parent: null };
        }
        let target: Array<ICompJson | null> = pageJson;

        //Sample path: [3,default,2] --> [ {},{},{},{C:{default: [{}, {}, {BURADASIN}]}}  ]
        for (let curPath of path) {
            if (new TypeHelper().isObject(target)) {
                parent = <any>target;
                target = parent!.C[curPath].c;
            } else { //array
                target = <any>target[(<any>curPath)];

            }
        }
        return { selfIndex: selfIndex, parentArray: target, parent: parent };
    }

    /**
     * @deprecated please use QJsonHelper.FindCompBySearchField
     */
    public findCompBySearchField(pageJson: Array<ICompJson>, value: string, componentPropertySearchKey: CompSearchKey = CompSearchKey.EditorID): ICompLocation | null{
        return QJsonHelper.FindCompBySearchField(pageJson, value, componentPropertySearchKey);
    }

    /**
     * Search the given pageJson with key and value parameters
     * @param pageJson Given pageJson
     * @param value To be searched value
     * @param componentPropertySearchKey Search key
     * @returns ICompLocation or null
     */
    public static FindCompBySearchField(pageJson: Array<ICompJson>, value: string, componentPropertySearchKey: CompSearchKey = CompSearchKey.EditorID): ICompLocation | null {
        let retVal: { compJson: ICompJson, curPath: Array<string | number> } | null = null;
        QJsonHelper.ForEachComp(<Array<ICompJson>>pageJson, (compJson, curPath) => {
            switch (componentPropertySearchKey) {
                case CompSearchKey.PID:
                    if (compJson.P.ID.H == value) {
                        retVal = { compJson, curPath: curPath! };
                        return false;
                    }
                    break;
                case CompSearchKey.Render:
                    if (compJson.P["Render"]?.H == value) {
                        retVal = { compJson, curPath: curPath! };
                        return false;
                    }
                    break;
                case CompSearchKey.EditorID:
                default:
                    if (compJson._Editor.eID == value) {
                        retVal = { compJson, curPath: curPath! };
                        return false;
                    }
                    break;
            }

            return true;
        });
        return retVal;
    }

    /**
     * Parse event parameters
     * @param eventParameters event parameters QS: x, y, z | TS: x: string, y: {a:number}, z: number
     */
    public static ParseParameters(eventParameters?: string, handlerLang?: ScriptLang): Array<{ name: string, type?: string }> | undefined {
        if (eventParameters) {
            const paramConverter: (item: string) => { name: string, type?: string } = handlerLang == "TS" || handlerLang == "JS" ? (item => ({ name: item.split(":")[0], type: item.split(":")[1] })) : (item => ({ name: item }));
            return eventParameters.split(",").map(paramConverter);
        }
        return undefined;
    }

    /**
     * Find component json path by EID
     * @returns path Sample path: [3,default,2]
     */
    public static FindCompPathByEID(qjson: Array<ICompJson>, compEID: string){
        let path: Array<string | number> | undefined;
        QJsonHelper.ForEachComp(qjson, (comp, curPath) => {if (comp._Editor.eID == compEID){ path = curPath; return false;} return true;});
        return path;
    }
}