import { ContextManager } from "../../../../shrimp/context";
import { Hook } from "../../../../shrimp/helpers/hook";
import { CType, CTyped, ICompJson } from "../../../../shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IComponent } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponent";
import { CreatedFieldsHook, IComponentCollection, ScopedSlotCreatorFunc } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { ICreatedComponent } from "../../../../shrimp/interfaces/ComponentInterfaces/ICreatedComponent";
import { IDirectiveCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IDirectiveCollection";
import { IEventCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IEventCollection";
import { IVnode } from "../../../../shrimp/interfaces/ComponentInterfaces/IVnode";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { IDictionaryNullable } from "../../../../shrimp/interfaces/IDictionaryNullable";
import { CompGroupType } from "../../../../shrimp/interfaces/quick/IWebSDK";
import { IDomElement } from "../../../../shrimp/interfaces/RenderingInterfaces/IDomElement";
import { IDoryJr } from "../../../../shrimp/interfaces/RenderingInterfaces/IDoryJr";
import { Dory } from "../../Dory";
import { HistoryManager } from "../../Managers/HistoryManager";
import { HistoryItem } from "../History/HistoryItem";
import { DirectiveCollection } from "./DirectiveCollection";
import { EventCollection } from "./EventCollection";

export class ComponentCollection implements IComponentCollection {
    public static IterateToChild(controlListOrItem: Array<IComponentCollection> | IComponentCollection, callback: (item: IComponentCollection) => boolean, options: { iterateDoryJrs?: boolean } = {}) {
        var traverse = function (controlList: Array<IComponentCollection>) {
            for (var index = controlList.length - 1; index >= 0; index--) {
                const item = controlList[index];

                if (callback(item) === false) {
                    return false;
                }

                if (item.children) {
                    for (const key in item.children) {
                        if (Object.prototype.hasOwnProperty.call(item.children, key)) {
                            const currentChildren = item.children[key];
                            if (currentChildren instanceof Function) {
                                return false;
                            }
                            if (traverse(currentChildren) === false) {
                                return false;
                            }
                        }
                    };
                }
                if (options?.iterateDoryJrs) {
                    const jrComps = item.GetDoryJr()?.GetLastHistory()?.GetComponentList();
                    if (jrComps) {
                        if (traverse(jrComps) === false) {
                            return false;
                        }
                    }
                }
            }

        }
        // let clonedObject = new TypeHelper().cloneObject(controlListOrItem);
        if (!(controlListOrItem instanceof Array)) {
            controlListOrItem = [controlListOrItem];
        }

        traverse(controlListOrItem);
    };

    collectionRef: { ref: ComponentCollection }
    component?: IComponent;
    domElement?: IDomElement;
    vDomElement?: IVnode;
    asChildType: CTyped;
    asChildName?: string;
    events: IEventCollection;
    compId: string;
    compEID: string;
    compType: string;
    children?: IDictionary<Array<IComponentCollection>>;
    childrenCreators?: IDictionaryNullable<ScopedSlotCreatorFunc>;
    context: ContextManager;
    childrenType?: CTyped;
    compGroupType?: CompGroupType;
    parentCompCollection?: IComponentCollection;
    class?: string;
    compDomClass?: string;
    public lockedFields?: Record<string, boolean>;
    private createdFieldsHook: Hook<CreatedFieldsHook>;
    private _componentVisibility: boolean = true;
    private compInstanceSetCb?: (compInstance: IComponent) => void;
    public asChildIndex: number;
    compareComponent: boolean = false;
    private doryJr?: IDoryJr;
    public dynamicCompIdSuffix?: string;
    private rootHistoryItem: HistoryItem; // dory level historyItem. Not custom comp or so..
    directiveFields: IDirectiveCollection;
    constructor({ compJson, context, asChildType = CType.child, asChildName, asChildIndex, parentCompCollection, compareComponent = true, dynamicCompIdSuffix }:
        {
            compJson?: ICompJson, context: ContextManager, asChildType?: CTyped, asChildName?: string, asChildIndex: number, parentCompCollection?: IComponentCollection, compareComponent?: boolean, dynamicCompIdSuffix?: string
        }) {
        const dory = context.GetItemSingle<Dory>(Dory.ContextName)!;
        this.events = new EventCollection(dory);
        this.directiveFields = new DirectiveCollection(dory);
        this.compId = compJson?.P.ID.H || "";
        this.compEID = compJson?._Editor.eID || "";
        this.compType = compJson?.T || "";
        this.context = context;
        this.asChildIndex = asChildIndex;
        this.childrenType = compJson ? compJson.O!.cType : undefined;
        this.asChildType = asChildType;
        this.asChildName = asChildName;
        this.collectionRef = { ref: this };
        this.parentCompCollection = parentCompCollection
        this.class = compJson?.P.class?.H;
        this.compDomClass = compJson?.O?.compDomClasses;
        this.createdFieldsHook = new Hook("CreatedFieldsHook");
        this.dynamicCompIdSuffix = dynamicCompIdSuffix;
        this.rootHistoryItem = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.getRootHM().currentLevel().last()!;
    }

    public get RootHistoryItem(): HistoryItem {
        return this.rootHistoryItem;
    }

    public SetChildrenFields({ children, childrenCreators }: { children?: IDictionary<Array<IComponentCollection>>, childrenCreators?: IDictionaryNullable<ScopedSlotCreatorFunc> }) {
        this.children = children;
        this.childrenCreators = childrenCreators;
    }

    public SetComponentCreatedFields({ createdComponent, keepAnEyeOnCreatedCompInst = false }: { createdComponent: ICreatedComponent, keepAnEyeOnCreatedCompInst?: boolean }) {

        if (keepAnEyeOnCreatedCompInst && !createdComponent.instance) {
            this.compInstanceSetCb = (compInstance: IComponent) => createdComponent.instance = compInstance;
        }
        if (createdComponent.instance) {
            this.compInstanceSetCb?.(createdComponent.instance);
        }
        if (this.domElement == createdComponent.el) { createdComponent.el = undefined; } //componentInstance setter on vNode "value.$mount()" might have changed $el. If not changed mark $el as unchanged (undef)
        this.component = createdComponent.instance || this.component;
        this.domElement = createdComponent.el || this.domElement;
        this.vDomElement = createdComponent.vel || this.vDomElement;
        this.compGroupType = createdComponent.compGroupType;
        this.createdFieldsHook.triggerer(createdComponent);
        this.lockedFields = createdComponent.lockedFields;

        //reApply visibility when instance is changed and comp should be invisible
        if (createdComponent.instance && !this.componentVisibility) {
            this.componentVisibility = false;
        }

        this.context.GetItemSingle<Dory>(Dory.ContextName)?.shellConfigs.createComponentQValue?.(this);
    }
    public get componentVisibility(): boolean {
        return this._componentVisibility;
    }
    public set componentVisibility(componentVisibility) {
        if (this.context.GetItemSingle<Dory>(Dory.ContextName)?.isEditorInEditState == true) { return; }
        if (this.lockedFields?.visible) { return; }

        this._componentVisibility = componentVisibility;
        this.context.GetItemSingle<Dory>(Dory.ContextName)?.shellConfigs.setVisibility(this, componentVisibility);
    }

    public WatchCreatedFields(cb: CreatedFieldsHook) {
        this.createdFieldsHook.subscribe(cb);
    }
    public SetInitialProps(props: IDictionary<any>): void {
        if (props.visible === false) {
            this._componentVisibility = false;
        }
    }
    /**
     * If component makes an internal rendering for a QJSON (ex: customComponent | renderingComponent) retrieves components internal doryJr.
     * Assumed max 1 doryJr
     */
    public GetDoryJr(): IDoryJr | undefined {
        return this.doryJr;
    }

    public SetDoryJr(doryJr: IDoryJr) {
        this.doryJr = doryJr;
    }

    public GetCompUID(): string {
        const compUIDPrefix = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.GetCompUIDPrefix();
        if (!compUIDPrefix) { return this.compId; }

        return compUIDPrefix + "." + this.compId;
    }
}