const javascript = { Object, String, Math, Date, Number, JSON };
interface ITSReturn {
    type: "quickReturnExc";
    retVal: any
}

const cacher = <T>(cacheFunc: (arg) => T) => {
    let retVal = {};
    return (...args) => {
        let n = args[0];
        if (n in retVal) {
            return retVal[n];
        } else {
            let result = cacheFunc(n);
            retVal[n] = result;
            return result;
        }
    }
};
const memoized = cacher(new QsExtensions().generateExtensions);

const handleReturn = function (cb: Function) {
    try {
        return cb();
    } catch (err) {
        if ((err as ITSReturn).type != "quickReturnExc") { throw err; }

        return (err as ITSReturn).retVal;
    }
}
const globalConstants = {
    ElementLocation: ElementLocation,
    LogType: LogType
};
function concatGlobalConstants(concatItems: (object: IDictionaryNullable<any> | null | undefined, globalKey?: string) => void) {
    Object.keys(globalConstants).forEach(key => concatItems(globalConstants[key], key));
}

export class Evaluator implements IEvaluator {
    public Evaluate2(options: { scriptName: string, pageScripting: PageScripting, dataSource?: object | null, context: ContextManager }) {
        const page = options.pageScripting.GetPageInstance();
        if (!page) { return undefined; }

        const func = page[options.scriptName];
        if (!func) { return undefined; }

        const { globals, responseContext, compInstDict } = this.prepareEvaluationItems(options.context);
        Object.keys(TsFilterKeysGlobals).forEach(key => delete globals[key]); //js :( // including this

        return func(responseContext.response, options.dataSource, globals, compInstDict);
    }

    public Evaluate({ script, dataSource, context, lang, sourceMap }: { script?: string | object, dataSource?: object | null, context: ContextManager, lang?: ScriptLang, sourceMap?: string }): any {
        if (!script) {
            return null;
        }
        if (typeof (script) !== "string") {
            if (lang != "FL") { return new TypeHelper().cloneObject(script); }
            return triggerFlow(context, script, dataSource, { getFormData, requestAsync, go, alert });
        }

        const { globals, responseContext, compInstDict, messagingManager, dory, lastHistory, ownerComponent } = this.prepareEvaluationItems(context);
        switch (lang) {
            case "JS":
                const thisContext = globals.this; // backup this before deletion
                Object.keys(TsFilterKeysGlobals).forEach(key => delete globals[key]); //js :( // including this
                const keys: Array<string> = ["null", "exports", "Promise", "response"]; // first one is this context in new Function(...) -> new (Function.apply(["null", ...])) -> new Function.prototype.bind.apply(Function, ["null", ...])
                const exports = {};
                const values: Record<string, any> = [exports, QPromise, responseContext.response];
                const concatItems = (object: IDictionaryNullable<any> | null | undefined, globalKey?: string) => {
                    const item = object || {};
                    if (!globalKey) {
                        Object.keys(item).forEach(key => { keys.push(key); values.push(item[key]) });
                    } else {
                        keys.push(globalKey); values.push(item);
                    }
                };
                concatItems(dataSource);
                concatItems(globals, "quick");
                concatGlobalConstants(concatItems);
                concatItems(compInstDict, "components");

                if (script.indexOf("//# sourceMappingURL=data:application/json;base64") > -1) {
                    keys.push("script"); values.push(script);
                    keys.push("return eval(script);");
                } else {
                    keys.push(script);
                }
                new TSProxy({ dory, globals }).generateTsProxy();
                const func: Function = new (Function.prototype.bind.apply<Function, Array<string>, () => void>(Function, keys));
                return handleReturn(() => func.apply(Object.create(null), values));
            case "QS":
            default:
                new QsProxyGenerator().generateProxies(globals, messagingManager, dory, ownerComponent);
                const extensions = memoized(dory);
                const forest = new Decompressor().Decompress(script);
                return handleReturn(() => Eval(forest, responseContext.response, dataSource, globals, compInstDict, extensions, context));

        }
    }

    private prepareEvaluationItems(context: ContextManager) {
        const messagingManager = new MessagingManager(context);
        const historyManager = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!;
        const lastHistory = historyManager ? historyManager.currentLevel().last() : null;
        const compInstDict = lastHistory && lastHistory.GetCompInstDict();
        const dory = context.GetItemSingle<Dory>(Dory.ContextName)!;
        const renderingContext = context.GetItemSingle<RenderingContext>(RenderingContext.ContextName);
        const ownerComponent = renderingContext && renderingContext.ownerComponent;

        const cachedEvalHelper = new CachedEvalHelper();

        const globals: IGlobalsQS = this.createGlobal({ messagingManager, context, dory, ownerComponent, lastHistory, historyManager });
        cachedEvalHelper.MergeEvalGlobals(globals);
        const responseContext = context.GetItemSingle<ResponseContext>(ResponseContext.ContextName)!;

        return { globals, responseContext, compInstDict, messagingManager, dory, lastHistory, ownerComponent };
    }

    public createGlobal({ messagingManager, context, dory, ownerComponent, lastHistory, historyManager }: { messagingManager?: MessagingManager, context: ContextManager, dory: Dory, ownerComponent?: IComponentCollection, lastHistory?: HistoryItem | null, historyManager: HistoryManager }): IGlobalsQS {
        const self = this;
        const storeManager = new StoreManager(context);
        const scopeContext = <ScopeContext | undefined>context.GetItemSingle<ScopeContext>(ScopeContext.ContextName);
        const valueContext = context.GetItemSingle<ValueContext>(ValueContext.ContextName)!;
        const engineYamlLogType = dory.shellConfigs.getMinEngineLogType?.();

        let globals: IGlobalsQS = {
            Request: {
                async: (requestObject: IRequest) => requestAsync(dory, context, requestObject),
                download(requestObject: IDownloadRequest) {
                    if (!dory.isEditorInEditState) {
                        const reqManager = new DownloadManager();
                        reqManager.RequestHandler(requestObject);
                    }
                }
            },
            MM: {
                get(eventName: string) {
                    return messagingManager?.Get(eventName);
                },
                trigger(eventName: string, parameters: Array<any> | undefined): any {
                    return messagingManager?.Trigger({ eventName: eventName, parameters: parameters });
                },
                delay(eventName: string, parameters?: { delayTime?: number, options?: Array<any> }) {
                    var timer = setTimeout(() => messagingManager?.Trigger({ eventName, parameters: parameters?.options ?? parameters }), parameters?.delayTime);
                    return timer;
                },
                clearTimeout(timer: any) {
                    clearTimeout(timer);
                }
            },
            Math: {
                floor: Math.floor,
                random: function (min: number, max: number) {
                    return min + Math.floor(Math.random() * Math.floor(max - min + 1));
                },
                findIndex: function (array: Array<Object>, searchKey: string, seacrhString: string) {
                    return array.findIndex(i => i[searchKey] == seacrhString);
                },
                round: function (roundNumber: number, indexToSplit: number) {
                    let classedNumber = Number(roundNumber);
                    let fixedNumber = classedNumber.toFixed(indexToSplit);
                    return Number(fixedNumber);
                }
            },

            EM: {
                trace: function (traceMessage: any, options?: { logType?: LogType }) {
                    const yamlLogType = dory.shellConfigs.getYamlLogType?.();
                    Logger.Log({ logType: options?.logType || LogType.Trace, logMessage: traceMessage, backgroundColor: "green", textColor: "white", source: "model", isClientLog: true, yamlLogType: (<LogType>yamlLogType) });
                }
            },
            currentPage: {
                isRenderCompleted: function (): boolean {
                    return dory.isRenderCompleted;
                }
            },
            LR: function (key: string, dataSource?: object | null) {
                //return dory.shellConfigs.lridHelper ? dory.shellConfigs.lridHelper(label) : null
                const historyItemBasedLRManager = context.GetItemSingle<HistoryItemBasedLRManager>(HistoryItemBasedLRManager.ContextName);
                return historyItemBasedLRManager?.GetResource({ key, dataSource });
            },
            importLocalization: function (langCode: string, localization: Record<string, string>, options: { overrite?: boolean } = {}) {
                const doryBasedLRManager = context.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName);
                doryBasedLRManager?.ImportResourceDict(langCode, localization, options);
            },
            go: function (...args) {
                if (dory.isEditorInEditState) { Logger.Log({ logMessage: "editor is not going anywhere in edit state", logType: LogType.Debug }); return; }
                const createrComp = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.getCreaterRenderingComponent();
                if (createrComp?.compType === "RenderingComponent" && createrComp?.component?.RenderGo) {
                    return createrComp?.component?.RenderGo(...args);
                }
                return dory.shellConfigs.go ? dory.shellConfigs.go(...args) : null;
            },
            regex: function (value: string) {
                return RegExp(value);
            },
            typeOf: function (value: any) {
                return typeof value;
            },
            getComponent: function (compID: string) {
                let compInstance = dory.GetComponentCollectionByID(compID) === undefined ? null : dory.GetComponentCollectionByID(compID)!.component;
                if (!compInstance) {
                    compInstance = dory.GetSubComponentCollectionByID(compID)?.component;
                }
                return compInstance;
            },
            setFormData: function (compCol: object) {
                let compCollection = dory.GetComponentCollection();
                if (compCollection) {
                    Object.keys(compCol).forEach(compName => {
                        if (compCollection && compCollection[compName]) {
                            dory.shellConfigs.setComponentValue!(compCollection[compName], compCol[compName]);
                        }
                    });
                }
            },
            getArrangedNestedData: function (obj: object, pairChar: string) {
                let baseString = ''
                let mapObj = {};
                let tempObj = {};
                const traverse = function (obj: object) {
                    Object.keys(obj).forEach(key => {
                        if (typeof obj[key] == 'object') {
                            baseString += key + pairChar
                            for (var val in obj[key]) {
                                if (typeof obj[key][val] != 'object') {
                                    tempObj[baseString + val] = obj[key][val];
                                    mapObj = { ...mapObj, ...tempObj };
                                    delete obj[key][val];
                                    tempObj = {};
                                }
                            }
                            traverse(obj[key])
                        }
                        else {
                            baseString != '' ? tempObj[baseString + key] = obj[key] : tempObj[key] = obj[key];
                            mapObj = { ...mapObj, ...tempObj };
                            tempObj = {};
                        }
                    })
                    baseString = ''
                }
                traverse(obj);
                return mapObj;
            },
            getFormData: function (componentQID?: string) { return getFormData(dory, componentQID) },
            getArrangedFormData: function (formData: object, pairChar: string) {
                let CompObj = {}
                let TempObj = {}
                const isPlainObject = obj => !!obj && obj.constructor === {}.constructor;
                const getNestedObject = formData => Object.entries(formData).reduce((result, [prop, val]) => {
                    prop.split(pairChar).reduce((nestedResult, prop, propIndex, propArray) => {
                        const lastProp = propIndex === propArray.length - 1;
                        if (lastProp) {
                            nestedResult[prop] = isPlainObject(val) ? getNestedObject(val) : val;
                        } else {
                            nestedResult[prop] = nestedResult[prop] || {};
                        }
                        return nestedResult[prop];
                    }, result);
                    return result;
                }, {});
                Object.keys(formData).forEach(function (key) {
                    if (key.includes(pairChar)) {
                        TempObj[key] = formData[key]
                        let tempNestedObj = getNestedObject(TempObj);
                        CompObj = self.mergeDeep(CompObj, tempNestedObj)
                        TempObj = {}
                    } else {
                        TempObj[key] = formData[key]
                        CompObj = { ...CompObj, ...TempObj }
                        TempObj = {}
                    }
                })
                return CompObj;
            },
            cryptography: {
                generateKeyPair: function () { return dory.shellConfigs?.cryptoGenerateKeyPair?.() },
                store: function (keyPair: Record<string, string>) { return dory.shellConfigs?.cryptoStore?.(keyPair) },
                hash: {
                    sha512: function (value: string) { return dory.shellConfigs?.cryptoSha512?.(value) }
                },
                sign: function (value: string, privateKey: string, options?: { digestAlgorithm: string | "sha512", padding: string | "pkcs15" }) { return dory.shellConfigs?.cryptoSign?.(value, privateKey, options) },

            },
            encoding: {
                atob(encodedString: string): string {
                    return atob?.(encodedString);
                },
                btoa(stringToEncode: string): string {
                    return btoa?.(stringToEncode);
                }
            },
            Quick: {
                // set region yazılacak
                //get tüm region yazılacak
                //get currentregion yazılacak
                host: {
                    trigger: function (functionName: string, params: Record<string, any>) {
                        //trigger( "startMiniApp", {applicainId :"dasd", x:5}); 
                        let pageNameArray = HistoryManager.traverseUpwards([], lastHistory);
                        return dory.shellConfigs.hostTrigger?.(functionName, params, pageNameArray);
                    }
                },
                region: {
                    setRegion: function (regionName: string) {
                        dory.shellConfigs.setRegion?.(regionName);
                        dory.ReRender();
                    },
                    getRegionList: function () {
                        return dory.shellConfigs.getRegionList?.();
                    },
                    getCurrentRegion: function () {
                        return dory.shellConfigs.getCurrentRegion?.();
                    },
                    getFormattingDefinitionByCurrentRegion: function () {

                        return dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.();
                    }
                },
                platform: {
                    isMobile: function () {
                        return dory.platformType == "iOS" || dory.platformType == "Android" ? true : false;
                    },
                    isWeb: function () {
                        return dory.platformType == "Web" ? true : false;
                    },
                    getPlatformType: function () {
                        return dory.platformType;
                    },
                    getDeviceId: function () {
                        return dory.shellConfigs.platform?.getDeviceId?.();
                    }
                },
                theme: {
                    setTheme: function (themeName: string) {
                        lastHistory?.themeConfiguration(context, themeName);
                        dory.shellConfigs.setTheme?.(themeName);
                    },
                    getThemeList: function () {
                        return dory.shellConfigs.getThemeList?.();
                    },
                    addTheme: function (themeName: string) {
                        dory.shellConfigs.addTheme?.(themeName);
                    },
                    removeTheme: function (themeName: string) {
                        dory.shellConfigs.removeTheme?.(themeName);
                    },
                },
                back: function (...args) {
                    const argControl = args && args.length >= 1 && Object.keys(args[0]).length >= 1 && args[0].withoutOverride;
                    return argControl ? dory.goHistoryBack({}) : dory.shellConfigs.triggerPreviousPage ?
                        dory.shellConfigs.triggerPreviousPage?.(args) : dory.goHistoryBack({});

                },
                goHistory: function (step: number) {
                    let navigationDemandType = INavigationDemandType.back;
                    if (step > 0) { navigationDemandType = INavigationDemandType.forward }
                    const ownerHistoryManager = lastHistory?.getOwnerHistoryManager();
                    if (ownerHistoryManager)
                        navigationDemandType == INavigationDemandType.back ?
                            ownerHistoryManager.Back({ step, ownerHistoryManager }) : ownerHistoryManager.Forward({ step, ownerHistoryManager });
                },
                go: (qjsonPath, options) => go(context, dory, qjsonPath, options),
                goNative: function ({ code, param }: { code: string, param?: Record<string, any> }) {
                    dory.shellConfigs.shellGlobalMethods?.goNative && dory.shellConfigs.shellGlobalMethods.goNative({ code, param });
                },
                redirect: function (url: string, options?: IUrlOptions) {
                    dory.shellConfigs.redirect && dory.shellConfigs.redirect({ url, options });
                },
                performance: function () {
                    return dory.shellConfigs.performance && dory.shellConfigs.performance();
                },
                copy: function (object: Object) {
                    let cloneObject = new TypeHelper().cloneObject(object);
                    return cloneObject;
                },
                setComponentsProperty: function (compId: string, propertyName: string, propertyValue: string) {
                    const compCollection: IComponentCollection | undefined = dory.GetComponentCollectionByID(compId);
                    let compSubCollection;
                    if (!compCollection) {
                        compSubCollection = dory.GetComponentSubCollection()?.find(collection => collection.compId == compId);
                    }
                    dory.shellConfigs.setComponentsProperty?.(compCollection || compSubCollection, propertyName, propertyValue);
                },
                download: function (responseFile: { data: string, name: string }) {
                    return dory.shellConfigs.downloadHandler?.(responseFile) ?? false;
                },
                isObject: function (obj: any) {

                    return (obj && typeof obj === 'object' && !Array.isArray(obj));
                },
                validation: {
                    required: (message?) => new ValidationManager(context).required(message),

                    minLength: (minLenght, message?) => new ValidationManager(context).minLength(minLenght, message),

                    maxLength: (maxLenght, message?) => new ValidationManager(context).maxLength(maxLenght, message),

                    email: (message?) => new ValidationManager(context).email(message),

                    iban: (message?) => new ValidationManager(context).iban(message),

                    regex: (regexVal, message?) => new ValidationManager(context).regex(regexVal, message),

                    tckn: (message?) => new ValidationManager(context).tckn(message),

                    vkn: (message?) => new ValidationManager(context).vkn(message)

                },
                validate: function (compEID: string, displayAlertBox: boolean, displayError?: boolean) {
                    return new ValidationManager(context).validate(compEID, displayAlertBox, displayError)
                },
                validateAll: function (displayAlertBox: boolean, displayError?: boolean) {
                    return new ValidationManager(context).validateAll(displayAlertBox, displayError)
                },
                resetValidation: function (compEID: string) {
                    return new ValidationManager(context).resetValidation(compEID)
                },
                reset: function (compEID: string) {
                    return new ValidationManager(context).reset(compEID)
                },
                createChildren: function ({ parentCompQId, templateChildName, newChildName }: { parentCompQId: string, templateChildName: string, newChildName?: string }) {
                    let parentCompPJson = lastHistory!.getComponentJsonByCompId(parentCompQId);
                    let createdComps = DynamicCompManager.createDynamicChildren({
                        templateChildName: templateChildName, parentCompId: parentCompQId, newChildName: newChildName,
                        historyItem: lastHistory!, context
                    });
                    const retVal = createdComps?.map(createdComp => { return createdComp.newComponent });
                    const appendTo = newChildName ? newChildName : templateChildName;
                    DynamicCompManager.appendDynamicCreatedComp({ newComponents: retVal!, context, historyItem: lastHistory!, parentComp: createdComps![0].parentComp, childName: appendTo });
                    return retVal;

                },
                createComponent: function ({ templateCompQID, newCompQID, dataSource, place, placeQID, childName }: { templateCompQID: string, newCompQID: string, dataSource?: any, place?: ElementLocation, placeQID?: string, childName?: string }) {
                    if (!templateCompQID || !newCompQID) {

                        Logger.Log({ logMessage: `Check TemplateCompQID: ${templateCompQID} and newCompQID: ${newCompQID}`, minEngineLogLevel: engineYamlLogType });
                        return;
                    }

                    const createdComp = DynamicCompManager.createDynamicComp({ newCompQID, templateCompQID, dataSource, context, historyItem: lastHistory!, placeQID });
                    if (!createdComp) { return; }
                    DynamicCompManager.appendDynamicCreatedComp({ newComponents: [createdComp.newComponent], context, historyItem: lastHistory!, parentComp: createdComp.parentComp, place, childName: childName || "default" });
                    return createdComp.newComponent.component;
                },
                deleteComponent: function (componentInstance: IComponent) {
                    const compCollection = lastHistory?.GetCompCollectionByCompInst(componentInstance);
                    if (!compCollection) {
                        Logger.Log({ logType: LogType.Warning, logMessage: "Component couldn' t be found at the current page", minEngineLogLevel: engineYamlLogType });
                        return;
                    }
                    dory.shellConfigs.deleteComponent?.(compCollection);
                    lastHistory?.destroyCollection(compCollection);
                },
                findCreateComponentRelative: function (sourceComp: IComponent, targetCompID: string) {
                    const historyItem = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().last()!;
                    return historyItem.FindCreateComponentRelative(sourceComp, targetCompID);
                },
                bind: function ({ bindedObject, fields }: { bindedObject: string | undefined, fields: Array<string> | object | undefined }) {
                    let propManager = context.GetItemSingle<PropCalcContext>(PropCalcContext.ContextName);
                    if (bindedObject != IPropSupport.store) {
                        Logger.Log({ logMessage: "Object is not supported yet", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
                        return;
                    }
                    if (!fields) {
                        Logger.Log({ logMessage: "Provide fields to be binded", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
                        return;
                    }
                    let typeHelper = new TypeHelper()
                    if (!typeHelper.isArray(fields)) {
                        Logger.Log({ logMessage: "Object is not supported yet", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
                        return;
                    }
                    if (typeHelper.isArray(fields)) {
                        (<Array<string>>fields).forEach(field => {
                            let storeContextItems = storeManager.getTargetContextItems(field);
                            propManager?.watch(storeContextItems, field);
                        });
                    }
                },
                setLanguage: function (LRType: string) {
                    if (dory.isEditorInEditState) { return; } // stop setLang in messaging preRender
                    return dory.SetLRType(LRType);
                },
                getLanguage: function () {
                    return dory.GetLRType();
                },
                updateComponent: function (Comp?: IComponent) {
                    return Comp?.$forceUpdate();
                },
                exportToXlsx: function ({ fileName, items, headers, sheetName}: { fileName: string,  items: Array<object>, headers: Array<object>, sheetName?: string }) {
                    dory.shellConfigs.exportToXlsxHandler?.({ fileName, items, headers, sheetName });
                },
                toLongDate: function (date: Date, formatType?: string) {
                    if (!formatType) {
                        formatType = "DD.MM.YYYY HH:MM";
                        var reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()
                        if (reg != undefined) {
                            formatType = reg.longDateFormat.value;
                        }
                        // formatType = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()!.longDateFormat.value ?? "DD.MM.YYYY HH:MM";
                    }
                    return new Formatter().dateToFormattedString(date, formatType);
                },
                toShortDate: function (date: Date, formatType?: string) {

                    if (!formatType) {
                        formatType = "DD.MM.YYYY";
                        var reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()
                        if (reg != undefined) {
                            formatType = reg.shortDateFormat.value;
                        }
                        // formatType = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()!.shortDateFormat.value ?? "DD.MM.YYYY";
                    }
                    return new Formatter().dateToFormattedString(date, formatType);
                },
                toLongTime: function (date: Date, formatType?: string) {
                    if (!formatType) {
                        formatType = "HH:MM:SS";
                        var reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()
                        if (reg != undefined) {
                            formatType = reg.longTimeFormat.value;
                        }
                        // formatType = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()!.longTimeFormat.value ?? "HH:MM:SS";
                    }
                    return new Formatter().dateToFormattedString(date, formatType);
                },
                toShortTime: function (date: Date, formatType?: string) {
                    if (!formatType) {
                        formatType = "HH:MM";
                        var reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()
                        if (reg != undefined) {
                            formatType = reg.shortTimeFormat.value;
                        }
                        // formatType = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()!.shortTimeFormat.value ?? "HH:MM";
                    }
                    return new Formatter().dateToFormattedString(date, formatType);
                },
                toDate: function (dateString: string, formatType?: string): Date {
                    if (!formatType) {
                        formatType = "DDMMYYYY";
                        var reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.();
                        if (reg != undefined) {
                            formatType = reg.displayDateFormat.value;
                        }
                        // formatType = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.()!.displayDateFormat.value ?? "DDMMYYYY";
                    }
                    return new Formatter().toDate(dateString, formatType);
                },

                toMoney: function (number: Number | string, formatType?: string, opt?: Intl.NumberFormatOptions, bigDecimal?: boolean) {
                    const reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.();
                    if (!formatType) {
                        formatType = "tr-TR";
                        if (reg != undefined) {
                            formatType = reg.moneyFormat.value;
                        }
                    }
                    if (!opt) {
                        const digits = reg?.digits.value;
                        if (digits !== undefined) {
                            opt = { minimumFractionDigits: +digits }
                        }
                    }
                    if (bigDecimal) {
                        return new Formatter().formatBigNumber(<string>number, formatType);
                    }
                    return new Formatter().formatNumber(<number>number, formatType, opt);
                },
                toDecimal: function (value: string, radixPoint?: string) {

                    // Return 0 as-is if value is undefined:
                    if (!value) { return 0; }

                    // Default radix point comes from settings, but could be set to eg. "." in opts:
                    if (!radixPoint) {
                        const reg = dory.shellConfigs.getFormattingDefinitionByCurrentRegion?.();
                        const radixPointValue = reg?.radixPoint.value;
                        if (radixPointValue !== undefined) {
                            radixPoint = radixPointValue;
                        }
                    }

                    if (!radixPoint) {
                        radixPoint = "";
                    }
                    // Build regex to strip out everything except digits, radix point and minus sign:
                    var regex = new RegExp("[^0-9-" + radixPoint + "]", "g");
                    value = value
                        .replace(/\((?=\d+)(.*)\)/, "-$1") // replace bracketed values with negatives
                        .replace(regex, '');       // strip out any cruft

                    const unformatted = radixPoint ? parseFloat(value.replace(radixPoint, '.')) : parseFloat(value);   // make sure decimal point is standard

                    // This will fail silently which may cause trouble, let's wait and see:
                    return !isNaN(unformatted) ? unformatted : 0;
                },
                toBigNumber: function (value: string) {
                    return new BigNumber(value);
                },
                setComponentClass: function (componentInstance: IComponent, classes: Array<string>) {
                    const compCollection = lastHistory?.GetCompCollectionByCompInst(componentInstance);
                    if (!compCollection || !classes) {
                        return;
                    }
                    dory.shellConfigs.setComponentClass?.(compCollection, classes);
                },
                setPageTitle: function (value: string) {
                    return dory.editor ? undefined : dory.shellConfigs.setPageTitle?.(value);
                },
                get dm(): ValueContext {
                    return valueContext;
                },
                // dm: valueContext,
                getEditor: function () {
                    return dory.editor;
                },
                get isEditMode(): boolean {
                    return dory.isEditorInEditState;
                },
                // isEditMode: dory.isEditorInEditState,
                getComponentIdByInstance: function (componentInstance: IComponent) {
                    const compCollection = lastHistory?.GetCompCollectionByCompInst(componentInstance);
                    if (!compCollection) {
                        Logger.Log({ logType: LogType.Warning, logMessage: "Component couldn' t be found at the current page", minEngineLogLevel: engineYamlLogType });
                        return;
                    }
                    return compCollection.compId;
                },
                screen: {
                    get XOffset(): Number | undefined {
                        return dory.shellConfigs.XOffset?.();
                    },
                    get YOffset(): Number | undefined {
                        return dory.shellConfigs.YOffset?.();
                    },
                    intoView: function (component: IComponent) { return dory.shellConfigs.intoView?.(component) },
                    toTop: function () { return dory.shellConfigs.toTop?.() }
                    // XOffset: dory.shellConfigs.XOffset?.(),
                    // YOffset: dory.shellConfigs.YOffset?.(),
                    // intoView: (component: IComponent) => dory.shellConfigs.intoView?.(component),
                    // toTop: () => dory.shellConfigs.toTop?.()
                },
                environment: {
                    get variable(): any {
                        return dory.shellConfigs.getEnvironmentVariable?.();
                    },
                    // variable: dory.shellConfigs.getEnvironmentVariable?.()
                },
                yamlLogType: {
                    get yamlLogType(): any {
                        return dory.shellConfigs.getYamlLogType?.();
                    },
                },

            },
            sso: {
                start: function ({ clientId, authorizeEndpoint, redirectUri, scope }: { clientId: string, authorizeEndpoint: string, redirectUri: string, scope: string }) {

                    var codeVerifier = self.generateRandomString(64);
                    var state = self.generateRandomString(32);
                    if (!scope || !clientId) {
                        const error = "scope or clientid is missing";
                        Logger.Log({ logMessage: error, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                        return;
                    }

                    self.generateCodeChallenge(codeVerifier).then(function (codeChallenge) {
                        if (!dory.shellConfigs.storeAccess?.setSessionStorage) {
                            Logger.Log({ logMessage: "storeAccess NotImplemented", logType: LogType.Error });
                            return;
                        }

                        dory.shellConfigs.storeAccess.setSessionStorage({ key: "code_verifier", value: codeVerifier });
                        dory.shellConfigs.storeAccess.setSessionStorage({ key: "state", value: state });
                        dory.shellConfigs.storeAccess.setSessionStorage({ key: "clientId", value: clientId });

                        var urlParams: IDictionary<string> = {
                            response_type: "code",
                            client_id: clientId,
                            code_challenge_method: "S256",
                            code_challenge: codeChallenge,
                            scope,
                            redirect_uri: redirectUri,
                            state
                        };
                        if (!dory.shellConfigs.redirectTo) {
                            Logger.Log({ logMessage: "redirectTo NotImplemented", logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                            return;
                        }

                        let suffix = authorizeEndpoint.indexOf("?") > -1 ? (authorizeEndpoint.endsWith("&") ? "" : "&") : "?";

                        var url = authorizeEndpoint + suffix + self.urlConcat(urlParams);
                        dory.shellConfigs.redirectTo({ url })

                    });
                },
                validate: function ({ urlParams, tokenEndpoint, type, onSuccess, onFail }: {
                    urlParams: Record<string, string>, tokenEndpoint: string, type: string, onSuccess: string, onFail: string
                }) {
                    const ownerComp = context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)?.ownerComponent;
                    if (!urlParams["code"]) {
                        const error = "urlParams code not exist";
                        Logger.Log({ logMessage: error, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                        ownerComp?.events.Trigger({ eventName: onFail, eventArguments: { error } });
                        return;
                    }
                    if (!urlParams["state"]) {
                        const error = "state not exist";
                        Logger.Log({ logMessage: error, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                        ownerComp?.events.Trigger({ eventName: onFail, eventArguments: { error } });
                        return;
                    }
                    if (!dory.shellConfigs.network || !dory.shellConfigs.storeAccess?.getItemFromSessionStorage) {
                        const error = "network or storeAccess NotImplemented";
                        Logger.Log({ logMessage: error, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                        //ownerComp?.events.Trigger({ eventName: onFail, eventArguments: { error } });
                        return;
                    }
                    const state = dory.shellConfigs.storeAccess?.getItemFromSessionStorage({ key: "state" })
                    if (urlParams["state"] != state) {
                        const error = "state not matched";
                        Logger.Log({ logMessage: error, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                        ownerComp?.events.Trigger({ eventName: onFail, eventArguments: { error } });
                        return;
                    }

                    const body = {
                        client_id: dory.shellConfigs.storeAccess?.getItemFromSessionStorage({ key: "clientId" }),
                        code_verifier: dory.shellConfigs.storeAccess?.getItemFromSessionStorage({ key: "code_verifier" }),
                        grant_type: "authorization_code",
                        redirect_uri: dory.shellConfigs.getUrl?.({ noQueryString: true }),
                        type: type,
                        code: urlParams["code"]
                    };
                    const data = Object.keys(body).map(dataKey => `${encodeURIComponent(dataKey)}=${encodeURIComponent(body[dataKey])}`).join("&").replace(/%20/g, "+");
                    const reqManager = new RequestManager(context);
                    reqManager.RequestHandler({
                        url: tokenEndpoint,
                        data,
                        blockRender: false,
                        onSuccess: onSuccess,
                        onFail: onFail,
                        http: 'post',
                        headers: { 'Content-type': "application/x-www-form-urlencoded" },
                        responseField: null
                    });
                }
            },
            store: {
                set: storeManager.set.bind(storeManager),
                get: storeManager.get.bind(storeManager),
                getAll: storeManager.getAll.bind(storeManager),
                delete: storeManager.delete.bind(storeManager)
            },
            cookie: {
                set: function (key: string, cookieValue: ICookieValue) {
                    if (!dory.shellConfigs.cookieAccess?.setCookie) {
                        return;
                    }
                    return dory.shellConfigs.cookieAccess?.setCookie({ key, cookieValue })
                },
                get: function (key: string) {
                    if (!dory.shellConfigs.cookieAccess?.getItemFromCookies) {
                        return;
                    }
                    return dory.shellConfigs.cookieAccess?.getItemFromCookies({ key })
                },
                getAll: function () {
                    if (!dory.shellConfigs.cookieAccess?.getAllCookies) {
                        return {};
                    }
                    return dory.shellConfigs.cookieAccess?.getAllCookies() || {};
                },
                delete: function (key: string) {
                    if (!dory.shellConfigs.cookieAccess?.removeItemFromCookies) {
                        return;
                    }
                    return dory.shellConfigs.cookieAccess?.removeItemFromCookies({ key })
                },
            },
            Url: {
                resolve: function ({ url }: { url: string }) {
                    // todo: this function should resolve url with settings.yaml
                    if (!dory?.siteSettings?.selectedProjectUrl) { return url; }
                    let resolvedUrl = dory.siteSettings.selectedProjectUrl + url;
                    return resolvedUrl;
                }
            },
            findControl: function (): void {
                Logger.Log({ logMessage: "Rezerved for future use", minEngineLogLevel: engineYamlLogType });
            },
            Date: {
                parse: function (value: string | number, month: number | null) {
                    let date = month && typeof value === "number" ? new Date(value, month, 1) : new Date(value);
                    return date;
                },
                now: () => new Date(),
                time: function () {
                    return new Date().getTime();
                },
                compare: function (first: Date | string, second: Date | string) {
                    let firstDate: string, firstMonth: string, firstYear: string, secondDate: string, secondMonth: string, secondYear: string;
                    /**
                     * Check if date is YYYY-M-D or YYYY-D-Y
                     * This format is not compatible for IE11
                     * Turn it into YYYY-MM-DD or YYYY-DD-MM
                     */
                    if (typeof first === "string") {
                        let firstSeparator = first.substring(4, 5);
                        let secondSeparator = first.substring(6, 7);
                        if (firstSeparator == "-" && secondSeparator == "-") {
                            let date = first.substring(7).length == 2 ? first.substring(7) : "0" + first.substring(7);
                            firstDate = first.substring(0, 4) + "-" + "0" + first.substring(5, 6) + "-" + date;
                        } else {
                            firstDate = first;
                        }
                    } else {
                        firstDate = first.toString();
                    }

                    if (typeof second === "string") {
                        let firstSeparator = second.substring(4, 5);
                        let secondSeparator = second.substring(6, 7);
                        if (firstSeparator == "-" && secondSeparator == "-") {
                            let date = second.substring(7).length == 2 ? second.substring(7) : "0" + second.substring(7);
                            secondDate = <string>second.substring(0, 4) + "-" + "0" + second.substring(5, 6) + "-" + date;
                        } else {
                            secondDate = second;
                        }
                    } else {
                        secondDate = second.toString();
                    }

                    //For cross browser support, date must be in YYYY-MM-DD format, other types cant to be parsed
                    let firstDateLocalized = Date.parse(firstDate);
                    let secondDateLocalized = Date.parse(secondDate);
                    if (firstDateLocalized == secondDateLocalized) {
                        return "Equal";
                    }
                    return firstDateLocalized > secondDateLocalized;
                },
                getTimezoneOffset: function (date: Date) {
                    return date.getTimezoneOffset();
                },
                ISOString: function () {
                    return new Date().toISOString();
                },
                differenceInDays(first: Date, second: Date) {
                    let firstDate = Date.parse(first.toString());
                    let secondDate = Date.parse(second.toString());
                    let timeDiff = Math.abs(secondDate - firstDate);
                    return Math.ceil(timeDiff / (1000 * 60 * 60 * 24));
                },
                differenceInMonths(first: Date, second: Date) {
                    if (first > second) [first, second] = [second, first];
                    let months = (second.getFullYear() - first.getFullYear()) * 12;
                    months += (second.getMonth() - first.getMonth());
                    const hasDayDifference = (second.getMonth() == first.getMonth() && first.getDate() > second.getDate());
                    if (hasDayDifference) {
                        months -= 1;
                    }
                    return months <= 0 ? 0 : months;
                }
            },
            Number: {
                parse: function (value: string) {
                    return Number(value);
                },
                parseFloat: function (value: string) {
                    return Number.parseFloat(value);
                },
                mod: function (firstValue: number, secondValue: number) {
                    return firstValue % secondValue;
                }
            },
            JSON: {
                parse: function (jsonString: string) {
                    return JSON.parse(jsonString);
                }
            },
            this: ownerComponent && ownerComponent.component,
            parent: {
                props: scopeContext?.GetProps(),
                events: scopeContext?.GetEvents()
            },
            string: {
                space: "\xa0",
                replace: function (string: string, searchValue: string, replaceValue: string) {
                    return string.replace(searchValue, replaceValue);
                }
            },
            process: {
                getMetadata: function () {
                    return dory.shellConfigs.processManager?.getMetadata ?
                        dory.shellConfigs.processManager?.getMetadata() :
                        storeManager.get("$process") ? storeManager.get("$process").Metadata : storeManager.get("process") ? storeManager.get("process") : undefined;
                },
                setMetadata: function ({ Metadata }: { Metadata: any }) {
                    if (dory.shellConfigs.processManager?.setMetadata) {
                        dory.shellConfigs.processManager?.setMetadata({ Metadata });
                        return;
                    }
                    let process = storeManager.get("$process") ?? {};
                    process.Metadata = Metadata;
                    storeManager.set('$process', process);
                },
                changeProcessMetadata: function (key: string = '', onSuccess: string | undefined) {
                    let onSuccessFunc = () => {
                        if (onSuccess) {
                            ownerComponent?.events.Trigger({ eventName: onSuccess });
                        }
                    }
                    if (dory.shellConfigs.processManager?.changeProcessMetadata) {
                        dory.shellConfigs.processManager?.changeProcessMetadata({ key, onSuccessFunc });
                        return;
                    }
                    else {// for Qscript and rshell, it is coming from osGlobalObjects.
                        dory.shellConfigs.osGlobalObjects?.process.changeProcessMetadata({ key, onSuccessFunc });
                    }
                }
            },
            Object: Object,
            js: javascript,
            return: function (retVal: any) {
                throw <ITSReturn>{ type: "quickReturnExc", retVal };
            }
        };

        const shellGlobalMethods = dory.shellConfigs.shellGlobalMethods;
        if (shellGlobalMethods?.alert) {
            globals.Quick.alert = options => alert(shellGlobalMethods, ownerComponent, historyManager, options);
        }
        if (shellGlobalMethods?.getDeepLinkParams) {
            globals.Url.getDeepLinkParams = () => {
                return shellGlobalMethods.getDeepLinkParams();
            }
        }
        const getUrl = dory.shellConfigs.getUrl;
        if (getUrl) {
            globals.Url.getUrl = () => {
                return getUrl();
            }
        }
        return globals;
    }


    async generateCodeChallenge(codeVerifier) {
        var digest = await crypto.subtle.digest("SHA-256",
            new TextEncoder().encode(codeVerifier));
        var array = new Uint8Array(digest);
        return btoa(String.fromCharCode(...array))
            .replace(/=/g, '').replace(/\+/g, '-').replace(/\//g, '_')
    }

    generateRandomString(length) {
        var text = "";
        var possible = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";

        for (var i = 0; i < length; i++) {
            text += possible.charAt(Math.floor(Math.random() * possible.length));
        }

        return text;
    }

    private fillComponentData(key: string, compMap: Map<any, any>, componentByKey: IComponent | undefined): Map<any, any> {
        if (componentByKey!["dataValue"]) {
            compMap.set(key, componentByKey!["dataValue"])
        }
        if (componentByKey!["valueData"]) {
            compMap.set(key, componentByKey!["valueData"])
        }
        if (componentByKey!["radioGroupValue"]) {
            compMap.set(key, componentByKey!["radioGroupValue"])
        }
        if (componentByKey!["constRateValue"]) {
            compMap.set(key, componentByKey!["constRateValue"])
        }
        if (componentByKey!["internalValue"]) {
            compMap.set(key, componentByKey!["internalValue"])
        }
        return compMap;
    }
    private isObject(item: any) {
        return (item && typeof item === 'object' && !Array.isArray(item));
    }

    private urlConcat(dict: IDictionary<string>) {
        var query: Array<string> = [];
        for (var key in dict) {
            query.push(this.encode(key) + '=' + this.encode(dict[key]));
        }
        return query.join('&');
    }
    private encode(str: string) {
        var replace = {
            '!': '%21',
            "'": '%27',
            '(': '%28',
            ')': '%29',
            '~': '%7E',
            '%20': '+',
            '%00': '\x00'
        };
        return encodeURIComponent(str).replace(/[!'\(\)~]|%20|%00/g, function (match) {
            return replace[match];
        });
    }
    /**
     * Deep merge two objects.
     * @param target
     * @param ...sources
     */
    private mergeDeep(target: object, ...sources: any) {
        if (!sources.length) return target;
        const source = sources.shift();

        if (this.isObject(target) && this.isObject(source)) {
            for (const key in source) {
                if (this.isObject(source[key])) {
                    if (!target[key]) Object.assign(target, { [key]: {} });
                    this.mergeDeep(target[key], source[key]);
                } else {
                    Object.assign(target, { [key]: source[key] });
                }
            }
        }

        return this.mergeDeep(target, ...sources);
    }
}


function requestAsync(dory: Dory, context: ContextManager, requestObject: IRequest) {
    if (!dory.isEditorInEditState) {
        const reqManager = new RequestManager(context);
        return reqManager.RequestHandler(requestObject);
    }
}


/**
* This method using for getting all form not empty data.
*/
function getFormData(dory: Dory, componentQID?: string) {
    let compMap = new Map();
    let CompObj = {};
    let tempCompCollection = {};
    if (componentQID) {
        let compColl = dory.GetComponentCollectionByID(componentQID) ?? dory.GetSubComponentCollectionByID(componentQID);
        if (!compColl) { return {}; }
        ComponentCollection.IterateToChild(compColl, (item) => { tempCompCollection[item.compId] = item; return true; })
    } else {
        tempCompCollection = Object.assign({}, dory.GetComponentCollection()!);
    }

    if (tempCompCollection != null) {
        Object.keys(tempCompCollection).forEach(function (key) {
            if (tempCompCollection && tempCompCollection[key]) {
                if (tempCompCollection[key].componentVisibility == false) {
                    if (!tempCompCollection[key].children) {
                        delete tempCompCollection![key];
                        return;
                    }
                    const deleteChildCompfromCollection = (childItems: IComponentCollection[]) => {
                        Object.values(childItems).forEach(childItem => {
                            delete tempCompCollection![childItem.compEID];
                            if (childItem.children && childItem.children.default) {
                                deleteChildCompfromCollection(childItem.children.default);
                            }
                        });
                    }
                    deleteChildCompfromCollection(tempCompCollection[key]!.children!.default);


                }
            }
        });
        Object.keys(tempCompCollection).forEach(item => {
            if (tempCompCollection && tempCompCollection[item]) {
                const compData = dory.shellConfigs.getComponentValue(tempCompCollection[item])
                if (compData) {
                    CompObj[item] = compData;
                }
            }
        });
    }
    return CompObj;
}
function go(context: ContextManager, dory: Dory, qjsonPath: string, options?: {newTab?: boolean, store?: boolean}) {
    if (dory.isEditorInEditState) { Logger.Log({ logMessage: "editor is not going anywhere in edit state", logType: LogType.Debug }); return; }
    const createrComp = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.getCreaterRenderingComponent();
    if (createrComp?.compType === "RenderingComponent" && createrComp?.component?.RenderGo && !options?.newTab) {
        return createrComp?.component?.RenderGo(qjsonPath);
    }
    return dory.shellConfigs.go2 ? dory.shellConfigs.go2(qjsonPath, options) : null;
}
function alert(shellGlobalMethods: IShellGlobalMethods, ownerComponent: IComponentCollection | undefined, historyManager: HistoryManager, options: Parameters<NonNullable<IGlobalsQS["Quick"]["alert"]>>[0]) {
    if (options.category) {
        options.category = options.category ?? AlertType.Error;
    }
    options.actionButtons?.filter(button => !AlertButtonType[button.type]).forEach(actionButton => { actionButton.type = AlertButtonType.Cancel; });
    const currentPageId = historyManager.getRootHM().currentLevel().last()!.pageId;
    shellGlobalMethods.alert!(options, { ownerComponent, currentPageId });
}