import { IGlobalMethods } from "../../../../common/qshell";
import { DomHelper } from "../../../../common/shrimp/helpers/domHelper";
import { Logger, LogType } from "../../../../common/shrimp/helpers/logger";
import { ElementLocation } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ElementLocation";
import { AlertType, ErrorSource, IActionButton } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IAlert";
import { CType } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IComponent } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { CreateLightComponentCollection, IComponentCollection } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IQJSon } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IQJson";
import { IAlertParameters } from "../../../../common/shrimp/interfaces/quick/IShellGlobalMethods";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { IDomElement } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { IRenderer } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IRendererConfig } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IRendererConfig";
import { ComponentManagerImpl } from "./ComponentManagerImpl";
import { DownloadManager } from "./DownloadManager";
import { ExportManager } from "./ExportManager";

export class GlobalMethodsImpl implements IGlobalMethods {

    contextName: string;
    parentInstance: any;
    private config?: IRendererConfig;
    private errorContainerTarget: IDomElement;
    static renderer?: IRenderer;
    static onCloseIsActive: Boolean = false;
    static settingsWrapper: IWebSDKSettingsWrapper;

    constructor({ contextName, parentInstance, config, errorContainerTarget, settingsWrapper }: { contextName: string, parentInstance: any, config?: IRendererConfig, errorContainerTarget: IDomElement, settingsWrapper: IWebSDKSettingsWrapper }) {
        this.contextName = contextName;
        this.parentInstance = parentInstance;
        this.config = config;
        this.errorContainerTarget = errorContainerTarget;
        GlobalMethodsImpl.settingsWrapper = settingsWrapper;
        if(GlobalMethodsImpl.settingsWrapper.settings.title) {
            this.setPageTitle(GlobalMethodsImpl.settingsWrapper.settings.title);
        }
        if(GlobalMethodsImpl.settingsWrapper.settings.favicon) {
            this.setFavicon(GlobalMethodsImpl.settingsWrapper.settings.favicon);
        }
    }

    public setRenderer(renderer) {
        GlobalMethodsImpl.renderer = renderer;
    }

    public alert(options: {
        title?: string, text?: string, category?: keyof typeof AlertType,
        actionButtons?: Array<IActionButton>,
        error?: { errorMessage?: string, errorCode?: string, errorSource?: keyof typeof ErrorSource },
    }, parameters: IAlertParameters) {

        if (this.config?.alert) {
            this.config.alert(options, parameters);
            return;
        }

        if (GlobalMethodsImpl.renderer) {
            let alertQjson = GlobalMethodsImpl.renderer.settingsQJsons.GetAlertQjson();
            if (alertQjson) {
                this.createAlert(alertQjson, options, parameters);
                return;
            }
        }

        let alertIcon: string | undefined;

        const compManagerImpl = new ComponentManagerImpl(this.contextName, this.parentInstance)

        switch (options.category?.toLowerCase()) {
            case AlertType.Info.toLowerCase():
                alertIcon = "mdi-information";
                break;
            case AlertType.Success.toLowerCase():
                alertIcon = "mdi-check-circle";
                break;
            case AlertType.Warning.toLowerCase():
                alertIcon = "mdi-alert-circle";
                break;
            case AlertType.Error.toLowerCase():
                alertIcon = "mdi-close-circle";
                break;
            default:
                break;
        }

        const alertProps = {
            border: "left",
            closeIcon: "mdi-close",
            color: options.category?.toLowerCase(),
            coloredBorder: true,
            dismissible: true,
            elevation: 4,
            icon: alertIcon
        };

        const alertTitleComp = GlobalMethodsImpl.createLabelComp(true, compManagerImpl, options.title);
        const alertTextComp = GlobalMethodsImpl.createLabelComp(false, compManagerImpl, options.text);

        const compColl = CreateLightComponentCollection({ compType: "VAlert", compId: "quick_alert_eval_" });
        const alertCompEl = compManagerImpl.createComponent({ componentName: compColl.compType, props: alertProps, events: undefined, asChildType: null, slots: { default: [alertTitleComp.vel, <any>options.error?.errorMessage ?? alertTextComp.vel,] }, scopedSlots: undefined, compClass: undefined, componentCollection: compColl });
        this.errorContainerTarget.firstChild?.remove();
        this.errorContainerTarget.appendChild(alertCompEl.el!);
    }

    private createAlert(qjson: IQJSon, options: {
        title?: string, text?: string, category?: keyof typeof AlertType,
        actionButtons?: Array<IActionButton>,
        error?: { errorMessage?: string, errorCode?: string, errorSource?: keyof typeof ErrorSource },
        additionals?: { props?: any, events?: any },
    }, parameters: IAlertParameters) {

        if (!GlobalMethodsImpl.renderer) {
            return;
        }

        const alertRoot = document.createElement("div");
        const alertRootParent = document.createElement("div");
        alertRootParent.appendChild(alertRoot);

        const childRenderer = GlobalMethodsImpl.renderer.CreateChildRenderer("quialert");
        childRenderer.SetCallbackDisplay((domEls, pageId, pageName, navDir, override) => {
            domEls.forEach((domEl) => {
                alertRoot.appendChild(domEl);
            });
        });
        let approveEventName: string | undefined, cancelEventName: string | undefined;
        const buttonApplier: Record<string, (eventName?: string) => void> = {
            Confirm: (eventName) => approveEventName = eventName,
            Cancel: (eventName) => cancelEventName = eventName
        };
        options.actionButtons?.forEach(actionButton => buttonApplier[actionButton.type](actionButton.clickEventName));
        const createEventTriggerer = (eventName?: string) => {
            return () => {
                if (!parameters?.ownerComponent || !eventName) { return; }
                parameters.ownerComponent.events.Trigger({ eventName });
            };
        };
        childRenderer.Render({
            qjson,
            props: { title: options.title, text: options.text, category: options.category, actionButtons: options.actionButtons, error: options.error, additionals: options.additionals },
            events: { onCancel: createEventTriggerer(cancelEventName), onConfirm: createEventTriggerer(approveEventName) },
            compParentInst: null,
            noHistory: true,
            pageId: parameters.currentPageId,
        });
    }

    private static createLabelComp(title: boolean, compManagerImpl: ComponentManagerImpl, labelText?: string) {
        let props = {
            text: labelText ?? "Default Text",
            tagType: "paragraph"
        }
        const compColl = CreateLightComponentCollection({ compType: "QLabel", compId: title ? "quick_alert_title_eval_" : "alert_quick_text_eval_" });
        return compManagerImpl.createComponent({ componentName: compColl.compType, props, asChildType: CType.slot, compClass: title ? "font-weight-bold" : undefined, componentCollection: compColl });
    }

    public performance() {
        return performance.now();
    }

    public getDeepLinkParams(): Record<string, string | undefined> {
        // TODO - move to Quick_Shrimp
        class UrlHelper {
            public static ParseQuery(queryString: string): Record<string, string | undefined> {
                var query = {};
                var pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
                for (var i = 0; i < pairs.length; i++) {
                    var pair = pairs[i].split('=');
                    query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || '');
                }
                return query;
            }
        }

        return UrlHelper.ParseQuery(window.location.search);
    }

    public dynamicCompHandler({ createdComps, compLocater, parentCollection, childName }:
        { createdComps: Array<IComponentCollection>, compLocater: ElementLocation, parentCollection?: IComponentCollection, childName: string }) {

        if (parentCollection && createdComps[0].vDomElement) {
            return new ComponentManagerImpl(this.contextName, this.parentInstance).addNewSlotToParentComp({ parentCompCollection: parentCollection, slots: createdComps.map(item => item.vDomElement!), compLocater: compLocater, childName });
        } else {
            return GlobalMethodsImpl.decideCompLocation({ createdComps: createdComps.map(item => item.domElement!), parentComp: parentCollection, location: compLocater, childName: childName! });
        }
    }

    /**
     * Place the new created component dom element in the page. 
     * If it' s editor page, it will not be rendered. It might be rendered at the end of page, but it brokes the page layout
     * @param createdComp New component dom element
     * @param parentComp Reference component dom element. If it is now exit, new comp will be append as a last child
     * @param location Indicates the new component will be placed
     */
    private static decideCompLocation({ createdComps, parentComp, location, childName }:
        { createdComps: Array<IDomElement>, parentComp?: IComponentCollection, location: ElementLocation, childName: string }) {

        if (parentComp) {
            if (parentComp.component?.customChildAppender) {
                parentComp.component?.customChildAppender({ childName, createdComps });
                return;
            }
            if (childName) {
                throw "NOTIMPLEMENTED";
            }
            const parentCompEl = parentComp.component?.$el!;
            switch (location) {
                case ElementLocation.Before: {
                    if (parentCompEl.children.length == 0) {
                        createdComps.forEach(createdComp => {
                            parentCompEl.appendChild(createdComp);
                        });
                    }
                    else {
                        createdComps.forEach(createdComp => {
                            parentCompEl.insertBefore(createdComp, parentCompEl.childNodes[0])
                        });
                    }
                    break;
                }
                case ElementLocation.After: {
                    createdComps.forEach(createdComp => {
                        parentCompEl.appendChild(createdComp);
                    });
                    break;
                }
                default:
                    createdComps.forEach(createdComp => {
                        document.getElementsByClassName("shellContainerTarget")[0].appendChild(createdComp);
                    });

                    break;
            }
        } else {
            createdComps.forEach(createdComp => {
                document.getElementsByClassName("shellContainerTarget")[0].appendChild(createdComp);
            });

        }
    }

    public deleteComponent(compCollection: IComponentCollection) {
        const compDomEl = compCollection.component?.$el;
        if (!compDomEl) {
            Logger.Log({ logMessage: "Component dom element couldn' t be found at the current page", minEngineLogLevel: this.getMinEngineLogType() });
            return;
        }
        compDomEl.remove();
    }

    public setPageTitle(value : string) {
        const title = document.getElementsByTagName("title");
        if(title.length != 0) {
            title[0].remove();
        }
        const newTitle = DomHelper.CreateDom("title");
        newTitle.text = value;
        document.head.prepend(newTitle);
    }
    public setFavicon(favicon : string) {
        GlobalMethodsImpl.settingsWrapper.settings.favicon = favicon;
        const newfavicon = DomHelper.CreateDom("link", { id: "qui-favicon", attrs: { href: favicon, rel: "icon" } });
        const oldicon = document.getElementById("qui-favicon"); 

        if(oldicon) {
            oldicon.remove();
        }
        document.head.prepend(newfavicon);
    }

    public exportToXlsxHandler({ fileName, items, headers, sheetName }: { fileName: string,  items: Array<object>, headers: Array<object>, sheetName?: string }) {
        var exportManager = new ExportManager({ fileName: fileName, items: items, headers: headers, sheetName: sheetName });
        exportManager.exportToXlsx();
    }

    public downloadHandler( responseFile: { data: string, name: string }) {      
        
        if (!responseFile) {
            return;
        }
        let downloadManager = new DownloadManager();
        let responseFilename: string = responseFile.name || "data.xls";
        // Check if response has headers(fully axios response) or headers are given from QJson
        // if (responseFile.headers || responseHeaders) {
        //     let headers = responseFile.headers || responseHeaders;
        //     responseFilename = downloadManager.extractFileNameFromHeader(headers) || responseFilename;
        // }
        let blobFile :any = responseFile;
        if (responseFile.data) {
            blobFile = responseFile.data;
        }
        return downloadManager.download(blobFile, responseFilename);
    }
    public setComponentClass(compCollection: IComponentCollection, classes: Array<string>) {
        if (!compCollection.component) {
            return
        }
        classes.forEach(classKey => {
            compCollection.component!.classes = compCollection.component!.classes || {}
            compCollection.component!.classes[classKey] = true
        });
        compCollection.component.$mount();
    };

    public setComponentsProperty(componentCollection: IComponentCollection, propertyName: string, propertyValue: any) {
        const setPropertyValueDeep = coll => {
            Object.keys(coll).map(item => {
                coll![item].forEach(comp => {
                    if (comp.component) {
                        comp.component[propertyName] = propertyValue;
                    }
                    if (comp?.children) {
                        setPropertyValueDeep(comp.children);
                    }
                });
            });
        }
        if (componentCollection?.component && !componentCollection?.children) {
            componentCollection.component[propertyName] = propertyValue;
        } else {
            setPropertyValueDeep(componentCollection.children);
        }
    }

    clearPageClose() {
        GlobalMethodsImpl.onCloseIsActive = false;
    }

    setPageClose() {
        GlobalMethodsImpl.onCloseIsActive = true;
        window.addEventListener("beforeunload", (event) => {
            if (GlobalMethodsImpl.onCloseIsActive) {

                GlobalMethodsImpl.renderer?.Trigger("onPageClose");
                // set a truthy value to property returnValue
                event.returnValue = "page closed";
            }
        });
    }
    intoView(component: IComponent) {
        (<HTMLElement>component.$el)?.scrollIntoView();
    };
    YOffset() {
        return window.scrollY;
    }
    XOffset() {
        return window.scrollX;
    };

    toTop() {
        const shellContainerDiv = document.getElementsByClassName("shellContainerTarget")[0];
        shellContainerDiv.scrollIntoView();
    };

    getEnvironmentVariable() {
        return GlobalMethodsImpl.settingsWrapper.settings.environment;
    };

    public setEnvironment(env: unknown) {
        GlobalMethodsImpl.settingsWrapper.settings.environment = env;
    };


    getYamlLogType() {
        const yamlLogType = GlobalMethodsImpl.settingsWrapper.settings.yamlLogType
        return yamlLogType ? LogType[yamlLogType] : LogType.Trace;
    }

    public setYamlLogType(logType: string) {
        GlobalMethodsImpl.settingsWrapper.settings.yamlLogType = LogType[logType];
    }
    getPageTitle() {
        const title = GlobalMethodsImpl.settingsWrapper.settings.title;
        console.log(title);
        return title;
    };
    getFavicon() {
        const favicon = GlobalMethodsImpl.settingsWrapper.settings.favicon;
        return favicon;
    }


    getMinEngineLogType() {
        return GlobalMethodsImpl.settingsWrapper.settings.minEngineLogLevel ? LogType[GlobalMethodsImpl.settingsWrapper.settings.minEngineLogLevel] : undefined;
    };
}