import { StoreManager } from "../../Managers/StoreManager";
import { ComponentCollection } from "../Component/ComponentCollection";
import { IndexManager } from "./IndexManager";


type multipleCompCollectionDict = IDictionary<Array<IComponentCollection | Array<any>>>;

export class HistoryItem implements IHistoryItem {

    private qjson: IQJSon;
    private context: ContextManager;

    controlDict: IDictionary<IComponentCollection> = {};
    controlList: Array<IComponentCollection> = [];
    private compInstDict?: Record<string, IComponent>;
    private compIndexedCollection?: WeakMap<IComponent, IComponentCollection>;
    pageId: string;
    pageName?: string;
    additionals?: any;
    private indexManager?: IndexManager;
    private pageScripting?: PageScripting;

    private renderingContext?: RenderingContext;
    compareCompValue: boolean = false;

    //Code 1234: additioanls parametresi kaldırılacak.
    constructor({ qjson, context, storeItems, pageId, pageName, additionals }: { qjson: IQJSon, context: ContextManager, storeItems?: IDictionary<any>, pageId: string, pageName?: string, additionals?: any }) {
        this.qjson = qjson;
        this.context = context;
        this.pageId = pageId;
        this.pageName = pageName;
        this.additionals = additionals;
        this.context.RegisterItem(new ResponseContext());
        this.context.RegisterItem(new PageStoreContext());
        this.context.RegisterItem(new HistoryItemBasedLRManager({ currentContext: context, qLang: this.getQLang() }));

        if (storeItems) {
            new StoreManager(this.context).bulkSet(storeItems);
        }

    }

    async Render({ compParentInst, noHistory }: { compParentInst?: any, noHistory?: boolean }) {
        PerfSingles.Instance().Start("HistoryItemRender");
        this.pageScripting = this.qjson.cFiles ? new PageScripting(this.qjson.cFiles) : undefined;
        this.pageScripting?.GetPageClass(); //initiate first page class initiation
        this.indexManager = undefined;
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);

        historyManager?.currentLevel().setMomentaryItem(this);
        const renderingContext = this.renderingContext = new RenderingContext({ compParentInst, historyBasedConf: dory?.shellConfigs.historyBasedConf! });
        this.context.RegisterItem(renderingContext);

        this.UpdateLRType();

        renderingContext.startRendering();
        this.compareCompValue = this.findComparisonCompAndReplace(this.qjson.cJson);
        const renderingManager = new RenderingManager({ pjson: this.qjson.cJson, context: this.context, controlDict: this.controlDict, qLang: this.getQLang(), compareCompValue: this.compareCompValue, pageScripting: this.pageScripting });
        await renderingManager.RenderRoot();
        renderingContext.postRenderRoot();

        this.controlList = renderingManager.controlList;

        if (!noHistory) {
            historyManager?.AddHistory(this);
        }

        renderingContext.endRendering();
        PerfSingles.Instance().End("HistoryItemRender");
    };

    public getOwnerHistoryManager() {
        return this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
    }

    public FindCreateComponentRelative(sourceComp: IComponent, targetCompID: string): IComponent | undefined {
        const compCol = this.GetCompCollectionByCompInst(sourceComp);
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const engineYamlLogType = dory?.shellConfigs.getMinEngineLogType?.();
        if (!compCol) { Logger.Log({ logMessage: "can not find source component", logType: LogType.Debug, minEngineLogLevel: engineYamlLogType }); return undefined; }

        if (!compCol.dynamicCompIdSuffix) {
            Logger.Log({ logMessage: `comp ${compCol.compId}[${compCol.compType}] is not a dynamic created component`, logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return undefined;
        }

        const searchID = DynamicCompManager.generateCompID(targetCompID, compCol.dynamicCompIdSuffix);
        const foundCompColl = this.getCompCollectionById(searchID);
        if (!foundCompColl) { Logger.Log({ logMessage: `can not find relative of ${targetCompID} with regards to ${compCol.compId}`, logType: LogType.Debug, minEngineLogLevel: engineYamlLogType }); return undefined; }

        return foundCompColl.component;
    }

    /**
     * Get component by tyoe of the component name
     * @param typeName Component type name( {T: ..., P: ..., ...})
     * @returns ComponentCollection | null
     */
    GetControlWithTypeName(typeName: string) {
        for (var item in this.controlDict) {
            if (this.controlDict[item].compType == typeName) {
                return this.controlDict[item];
            }
        }
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const engineYamlLogType = dory?.shellConfigs.getMinEngineLogType?.();
        Logger.Log({ logMessage: `Component can not be found with type: ${typeName}`, minEngineLogLevel: engineYamlLogType });
        return null;
    }

    GetCompCollectionByCompInst(component: IComponent): IComponentCollection | null {
        if (!this.compIndexedCollection) {
            this.extractCompInstDictFrom(this.controlDict);
        }
        return this.compIndexedCollection?.get(component) || null;
    }

    public getComponentCollectionByIdProperty({ id, deep = false }: { id: string, deep: boolean }): IComponentCollection | undefined {
        if (!this.indexManager) { this.indexManager = new IndexManager(this.controlDict); }
        let compColl = this.indexManager.getComponentCollectionByIdProperty(id);
        if (!deep || compColl) { return compColl; }

        const historyItems = this.context.GetItemGroup<DoryJr>(DoryJr.ContextName)?.map(doryJr => doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last());
        historyItems?.find(historyItem => compColl = historyItem?.getComponentCollectionByIdProperty({ id, deep }));
        return compColl;
    }

    public getComponentCollectionByDom({ element, deep = false }: { element: IDomElement, deep: boolean }): IComponentCollection | undefined {
        if (!this.indexManager) { this.indexManager = new IndexManager(this.controlDict); }
        let compColl = this.indexManager.getComponentCollectionByDom(element);
        if (!deep || compColl) { return compColl; }

        const historyItems = this.context.GetItemGroup<DoryJr>(DoryJr.ContextName)?.map(doryJr => doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last());
        historyItems?.find(historyItem => compColl = historyItem?.getComponentCollectionByDom({ element, deep }));
        return compColl;
    }

    public getComponentSubCollectionByID({ id }: { id: string }): IComponentCollection | undefined {
        let compColl: IComponentCollection | undefined;

        const arrangeControlDict = (item: Array<DoryJr>) => item.map(doryJr => {
            if (!doryJr) return;
            let historyItem = doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
            if (historyItem && historyItem.controlDict && historyItem.controlDict[id]) {
                compColl = historyItem.controlDict[id];
                return;
            }
            else {
                let doryCtx = doryJr.ContextJr.GetItemGroup<DoryJr>(DoryJr.ContextName);
                if (!doryCtx) {
                    const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
                    const engineYamlLogType = dory?.shellConfigs.getMinEngineLogType?.();
                    Logger.Log({ logMessage: `Cannot find component at sub dictionary for ${id}`, logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
                    return;
                }
                arrangeControlDict(doryCtx);
            }
        });
        let doryJr = this.context.GetItemGroup<DoryJr>(DoryJr.ContextName);
        if (doryJr) arrangeControlDict(doryJr);
        return compColl;
    }

    private iterateSubHistoryItems<R>(cb: (historyItem: HistoryItem) => R): R | undefined {
        let doryJrs = this.context.GetItemGroup<DoryJr>(DoryJr.ContextName);
        if (!doryJrs) { return undefined; }

        const historyItems = doryJrs.map(doryJr => doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().last())
            .filter((historyItem): historyItem is HistoryItem => historyItem != undefined);
        return ArrayHelper.findAndRet(historyItems, cb);
    }

    public GetComponentCollectionByUID(compUID: string): IComponentCollection | undefined {
        const targetComp = CompUIDHelper.GetRootCompId(compUID);
        const candidate = this.controlDict[targetComp.rootCompId];
        if (!candidate) { return undefined; }
        if (!targetComp.subCompUID) { return candidate; }

        return this.iterateSubHistoryItems(hi => hi.GetComponentCollectionByUID(targetComp.subCompUID));
    }

    public GetComponentCollection(): Record<string, IComponentCollection> | null {
        return this.controlDict;
    }
    public GetComponentList(): Array<IComponentCollection> {
        return this.controlList;
    }

    public getCompCollectionById(id: string | IComponent): IComponentCollection | undefined {
        if (typeof id == "string") {
            return this.controlDict[id] || undefined;
        } else {
            return this.controlDict[id["_renderingProps"].props().ID] || undefined;
        }
    }

    public destroyComponentCollectionById(Id: string): boolean {
        if (typeof Id == "string") {
            this.destroyCollection(this.getCompCollectionById(Id));
            return true;
        }
        return false;
    }

    public update() {
        ComponentCollection.IterateToChild(this.controlList, item => {
            if (item.component && item.asChildType != CType.slot && item.component.$forceUpdate) {
                item.component.$forceUpdate();
            }
            return true;
        });
    }

    public hibernate() {
        ComponentCollection.IterateToChild(this.controlList, item => {
            // child yapısı:   slot > slot > child > ..   olan bir yapıda bir dal için child'larının slot olmasına bakmak yetmez. O daldaki her bir comp.'a ayrı ayrı bakılıp child'ların peşi toplanmalı
            if (item.component && item.asChildType != CType.slot && item.component.hibernate) {
                item.component.hibernate();
            }
            return true;
        }, { iterateDoryJrs: true });
    }

    public resurrect() {
        this.UpdateLRType();
        ComponentCollection.IterateToChild(this.controlList, item => {
            // child yapısı:   slot > slot > child > ..   olan bir yapıda bir dal için child'larının slot olmasına bakmak yetmez. O daldaki her bir comp.'a ayrı ayrı bakılıp child'ların peşi toplanmalı
            if (item.component && item.asChildType != CType.slot && item.component.resurrect) {
                item.component.resurrect();
            }
            return true;
        }, { iterateDoryJrs: true });
    }


    public destroyCollection(compCollection?: IComponentCollection) {
        this.destroyPriv(true, compCollection);
    }
    public destroy() {
        this.controlList.forEach(item => this.destroyPriv(false, item));
    }

    /**
     * Do not use. -> destroyCollection or destroy
     */
    private destroyPriv(obliviateComponent: boolean, compCollection?: IComponentCollection) {
        if (!compCollection) { return; }
        ComponentCollection.IterateToChild(compCollection, item => {
            // child yapısı:   slot > slot > child > ..   olan bir yapıda bir dal için child'larının slot olmasına bakmak yetmez. O daldaki her bir comp.'a ayrı ayrı bakılıp child'ların peşi toplanmalı
            if (item.component && item.component.$destroy) {
                item.component.$destroy();
                delete item.component;
            }

            if (obliviateComponent) {
                this.obliviateComponent(item);
            }

            return true;
        });
    }
    public remount() {
        ComponentCollection.IterateToChild(this.controlList, item => {
            if (item.component && item.component.$mount) {
                const mountComp = (comp: IComponent) => {
                    if (comp.$children) {
                        comp.$children.forEach(child => mountComp(child));
                    }
                    comp.$mount();
                };
                mountComp(item.component);
            }
            return true;
        });
    }

    public registerDoryJr(doryJr: IDoryJr) {
        this.context.RegisterItem(doryJr);
    }

    public getQLang(): ScriptLang | undefined {
        return this.qjson.opt?.QL;
    }

    public GetRenderingContext() {
        return this.renderingContext!;
    }
    public GetDoryJrs() {
        return this.context.GetItemGroup<DoryJr>(DoryJr.ContextName);
    }

    /**
     * Do not use directly, use Dory.SetLRType.
     */
    public UpdateLRType() {
        this.arrangeLRType();
    }

    private arrangeLRType() {
        if (!this.qjson.clrids) { return };

        let doryBasedLRManager = this.context.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName);
        let LRType = doryBasedLRManager?.LRType!;
        let historyItemBasedLRManager = this.context.GetItemSingle<HistoryItemBasedLRManager>(HistoryItemBasedLRManager.ContextName);
        historyItemBasedLRManager?.SetResourceDict(this.qjson.clrids[LRType], LRType);
    }

    private obliviateComponent(item: IComponentCollection) {
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const deleteComp = (comp: IComponentCollection) => {
            delete this.controlDict[comp.compId];
            if (this.compInstDict) { this.compInstDict[comp.compId] }
            delete this.compInstDict?.[comp.compId];
            const parent = comp.parentCompCollection;
            if (parent) {
                const childNameKeys = Object.keys(parent.children!);
                ArrayHelper.findAndRet(childNameKeys, (childName => {
                    const index = parent.children![childName].findIndex(childColl => childColl == comp);
                    if (index != -1) {
                        dory?.shellConfigs.onComponentDestroy?.({ parentComponent: parent.component!, childKey: childName, childVDomElement: comp.vDomElement! })
                        parent.children![childName].splice(index, 1);
                    }

                }));
            } else {
                const index = this.controlList.findIndex(collection => collection == comp);
                this.controlList.splice(index, 1);
            }
            this.compIndexedCollection?.delete(comp.component!);
        }
        deleteComp(item);
    }

    public GetCompInstDict() {
        if (this.compInstDict) { return this.compInstDict; }

        this.extractCompInstDictFrom(this.controlDict);
        return this.compInstDict || {};
    }

    public ReCalculateCompInstDict() {
        this.compInstDict = undefined;
        this.compIndexedCollection = undefined;
    }

    private extractCompInstDictFrom(dict: IDictionary<IComponentCollection>) {
        if (this.GetRenderingContext().IsRendering()) { return; }

        this.compInstDict = {};
        this.compIndexedCollection = new WeakMap();
        Object.keys(dict).forEach((compPID) => {
            const curColl = dict[compPID];
            if (curColl.component) {
                this.compIndexedCollection!.set(curColl.component, curColl);
                this.compInstDict![compPID] = curColl.component;
            } else {
                curColl.WatchCreatedFields((createdFields) => {
                    if (!this.compIndexedCollection || !this.compInstDict) { return; }

                    this.compIndexedCollection.set(curColl.component!, curColl);
                    this.compInstDict[compPID] = curColl.component!;
                });
            }
        });
    }

    getComponentJsonByCompId(compId: string) {
        if (!this.qjson.cJson) {
            return null;
        }
        const compLoc = QJsonHelper.FindCompBySearchField(this.qjson.cJson, compId, CompSearchKey.PID)!;
        const path = QJsonHelper.FindParentArrayPath(compLoc.curPath, this.qjson.cJson);
        return { compLoc, path };
    }

    getComponentSubCollections(): Array<IComponentCollection> {
        const dicts: IComponentCollection[] = [];
        this.context.GetItemGroup<DoryJr>(DoryJr.ContextName)?.forEach(doryJr => {
            const dict = doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last()?.controlDict;
            if (dict) {
                Object.keys(dict).forEach(key => dicts.push(dict[key]));
            }
        });

        return dicts;
    }
    /**
     * Find comparison component at the current pjson
     * @param pjson Current pjson
     * @returns boolean
     */
    private findComparisonCompAndReplace(pjson: Array<ICompJson>) {
        if (!pjson) {
            return false;
        }
        let hasCompareComp = false;
        QJsonHelper.ForEachComp(pjson, (compJson, path) => {
            if (compDiffHelper.comparisonComponent == compJson.T) {
                hasCompareComp = true;
                return false;
            }
            return true;
        });
        if (hasCompareComp) {
            QJsonHelper.ForEachComp(pjson, (compJson, path, info) => {
                if (compDiffHelper.componentValueList[compJson.T] && !compDiffHelper.componentValueList[compJson.T].classCompare && info?.parentCompJson &&
                    info.parentCompJson.T != ComparisonEnum.tooltipCompType && !compDiffHelper.componentValueList[info.parentCompJson.T]?.classCompare) {
                    compDiffHelper.replaceTooltipComponent(info.parentCompJson, compJson);
                }
                return true;
            })
        }
        return hasCompareComp;
    }

    themeConfiguration(context: ContextManager, themeName: string) {
        if (!(themeName == "light" || themeName == "dark")) return;

        const historyManager = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        const dory = context.GetItemSingle<Dory>(Dory.ContextName);
        const isDark = dory!.isDark = themeName == "dark" ? true : undefined;

        const rootControlList = historyManager?.getRootHM().currentLevel().last()?.controlList;
        if (!rootControlList) return;
        setCompPropAndJrHistory(rootControlList);

        function setCompPropAndJrHistory(compList: Array<IComponentCollection>) {
            for (let index = 0; index < compList.length; index++) {
                const compCollection = compList[index];
                const comp: any = { children: { default: [compCollection] } };
                dory?.shellConfigs.setComponentsProperty?.(comp, "dark", isDark);
                dory?.shellConfigs.setDarkTheme?.(compCollection, isDark || false);
                const compDoryJrCompList = compCollection.GetDoryJr()?.GetComponentList();
                if (compDoryJrCompList) {
                    setCompPropAndJrHistory(compDoryJrCompList);
                }
            }
        }
        // return true;
    }
}