import { IContextDepTarget, PropCalcContext } from "./PropCalcContext";

export class PropDepTracker {

    /**
     * Create a new object with one way binded
     * @param bindObject Object on which to add or modify the property. This can be a native JavaScript object (that is, a user-defined object or a built in object) or a DOM object.
     * @param field The property name
     * @param depGetter Get method of subscribed fields
     * @param depTriggerer Trigger method of subscribers
     */
    createDepTracker(bindObject: any, field: string | number | symbol, depGetter: (context: PropCalcContext) => IContextDepTarget, depTriggerer: Function, propCalcContext: PropCalcContext) {
        let subscribedFields: Array<IContextDepTarget> = []
        let fieldValue: any;
        Object.defineProperty(bindObject, field, {
            set: (value) => {
                fieldValue = value;
                this.propSetter(depTriggerer, subscribedFields, propCalcContext);
            },
            get: () => {
                this.propGetter(depGetter, subscribedFields, propCalcContext);
                return fieldValue
            }, enumerable: true,
            configurable: true
        }
        );
    }

    /**
     * Getter of one way binded object
     * @param depGetter Get method of subscribed fields
     * @param subscribers Subscriber list of new object
     * @param retVal Return value of new object
     */
    private propGetter(depGetter: (context: PropCalcContext) => IContextDepTarget, subscribers: Array<IContextDepTarget>, propCalcContext: PropCalcContext) {
        var propT = depGetter(propCalcContext);
        subscribers.push(propT);
    }

    /**
     * Setter of one way binded object
     * @param deepTriggerer Trigger method of subscribers
     * @param subscribers Subscriber list of new object
     */
    private propSetter(deepTriggerer: Function, subscribers: Array<IContextDepTarget>, propCalcContext: PropCalcContext) {
        subscribers.forEach(item => deepTriggerer({ depTarget: item, self: propCalcContext}));
    }
}