import { ContextManager } from "../../../shrimp/context";
import { Hook } from "../../../shrimp/helpers/hook";
import { ILRID } from "../../../shrimp/interfaces/ComponentInterfaces/ILocalResource";
import { IQJSon } from "../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { INavigationDemand } from "../../../shrimp/interfaces/quick/INavigationDemand";
import { PlatformType } from "../../../shrimp/interfaces/quick/IPlatform";
import { ISiteSettings } from "../../../shrimp/interfaces/quick/ISiteSettings";
import { DisplayHookCb, IDory, PartialDisplayHookCb } from "../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { IPageCompletedCb, IPageRenderStartedCb } from "../../../shrimp/interfaces/RenderingInterfaces/ILifeCycleCb";
import { IDoryRenderer, IRenderer, ISettingsQJsonContext } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IRendererOperatorCollection } from "../../../shrimp/interfaces/RenderingInterfaces/Operators/IRendererOperatorCollection";
import { IRegionalManager, RegionalManagerContextName } from "../interfaces/construction/IRegionalManager";
import { DoryHelper } from "./DoryHelper";
import {  QjsonData, QJsonPool } from "./QJsonHelpers/QJsonPool";
import { DoryRendererChild, RendererChild } from "./RendererChild";

export class Renderer implements IRenderer {
    public context: ContextManager;
    protected Dory: IDory;
    settingsQJsons: ISettingsQJsonContext;
    public readonly BeforeRenderStartHook = new Hook<() => void>("BeforeRenderStartHook");

    public get PageCompletedHook(): Hook<IPageCompletedCb> {
        return this.Dory.PageCompletedHook;
    }
    public get PageRenderStartedHook(): Hook<IPageRenderStartedCb> {
        return this.Dory.PageRenderStartedHook;
    }
    public get DisplayHook(): Hook<DisplayHookCb> {
        return this.Dory.DisplayHook;
    }
    constructor({ context, doryInstanceName, operatorCollection }: { context: ContextManager, doryInstanceName?: string, operatorCollection: IRendererOperatorCollection }) {
        this.context = context;
        this.Dory = new DoryHelper({ context: this.context, doryInstName: doryInstanceName, renderer: this, operatorCollection }).Dory;
        this.settingsQJsons = this.Dory.SettingsQJsonContext;
    }
   
    async Render({ pjsonPath, compParentInst, storeItems, pageId, pageName, pjsonContent }:
        { pjsonPath?: string; compParentInst?: any; storeItems?: IDictionary<any>, pageId?: string, pageName?: string, pjsonContent?: IQJSon }) {
        this.BeforeRenderStartHook.triggerer();
        const qjsonPool = this.context.GetItemSingle<QJsonPool>(QJsonPool.contextName);
        if (!pjsonContent && !pjsonPath) {
            return;
        }

        const qjsonData: QjsonData = pjsonContent ? { type: "content", content: pjsonContent } : { type: "path", path: pjsonPath! };
        let qJSon = await qjsonPool?.retrieveAndFillQjsonAsync({ qjson: qjsonData, dory: this.Dory })
        if (!qJSon) {
            return;
        }

        pageName = pageName ?? (pjsonPath ? this.getPageName(pjsonPath) : Math.random().toString());

        this.Dory.Render({ qjson: qJSon, compParentInst, storeItems, pageId, pageName });
    }
    public CreateChildRenderer(compUIDPrefix?: string) {
        return new DoryRendererChild(this, compUIDPrefix);
    }
    public SetLRType(LRType?: string) {
        this.Dory.SetLRType(LRType);
        const region = this.context.GetItemSingle<IRegionalManager>(RegionalManagerContextName);
        region?.onLRChange?.(LRType?.substring(0, 2));
    }
    public SetGlobalLRDict(GlobalLRDict?: ILRID) {
        this.Dory.SetGlobalLRDict(GlobalLRDict);
    }
    public SetSiteSettings(siteSettings: ISiteSettings): void {
        this.Dory.SetSiteSettings(siteSettings);
    }
    public SetPlatformType(platformType: PlatformType): void {
        this.Dory.SetPlatformType(platformType);
    }
    public SetDisplayCallBack(callBackFunc: DisplayHookCb) {
        this.Dory.SetDisplayCallBack({ callBackFunc });
    };
    public SetPartialDisplayCallBack(callBackFunc: PartialDisplayHookCb) {
        this.Dory.SetPartialDisplayCallBack({ callBackFunc });
    };


    public GetNavigationDemand(pageId: string) {
        return this.Dory.hasHistory(pageId);
    }
    public GoHistory(navigationDemand: INavigationDemand) {
        this.Dory.goHistory({ navigationDemand });
    }
    public Back() {
        // window.history.back();
        this.Dory.goHistoryBack();
    }
    public Forward() {
        // window.history.forward();
        this.Dory.goHistoryForward();
    }
    public Clear() {
        this.Dory.Clear();
    }
    public Hibernate(): void {
        this.Dory.CallHibernate();
    }
    public CheckComponent({ compName }: { compName: string }): Promise<any> | undefined {
        const qjsonPool = this.context.GetItemSingle<QJsonPool>(QJsonPool.contextName);
        return qjsonPool?.ModuleManager.CheckComponent(compName, undefined);
    }
    public GetComponentCollection() {
        return this.Dory.GetComponentCollection();
    }
    private getPageName(pjsonPath: string) {
        const qjsonIndex = pjsonPath.indexOf(".qjson");
        if (qjsonIndex < 0) {
            return pjsonPath;
        }

        return pjsonPath.substring(0, qjsonIndex);
    }
    public Trigger(eventName: string, parameters: Record<string, any>): any {
         return this.Dory.Trigger(eventName, parameters);
    }
    public SetEditMode(state: boolean) {
        this.Dory.SetEditMode(state);
    }
}

export class DoryRenderer extends Renderer implements IDoryRenderer {
    public get DoryInst() {
        return this.Dory;
    }
}