import { ContextManager } from "../../../../shrimp/context";
import { Logger, LogType } from "../../../../shrimp/helpers/logger";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDynamicPropComponent } from "../../../../shrimp/interfaces/ComponentInterfaces/IDynamicComponentProp";
import { IExternalEvents } from "../../../../shrimp/interfaces/ComponentInterfaces/IExternalEvents";
import { IExternalProp } from "../../../../shrimp/interfaces/ComponentInterfaces/IExternalProp";
import { IQJSon } from "../../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { TSComponentBase } from "../../../../shrimp/interfaces/ComponentInterfaces/TSComponentBase";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { CreatePromiseData, IPromiseData } from "../../../../shrimp/interfaces/IPromiseData";
import { PartialDisplayHookCb } from "../../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { IDoryJr } from "../../../../shrimp/interfaces/RenderingInterfaces/IDoryJr";
import { GlobalContext } from "../../Context/GlobalContext";
import { RenderingContext } from "../../Context/RenderingContext";
import { Dory } from "../../Dory";

export default class CustomComponent extends TSComponentBase implements IDynamicPropComponent {
    protected qJsonPath: string;
    protected props: IDictionary<any>
    protected events: IDictionary<Function>
    protected parentInstance: any
    protected dory: Dory | undefined;
    public doryJr: IDoryJr | undefined;
    protected context: ContextManager;
    protected containerView: unknown;
    protected renderingContext: RenderingContext;
    protected tsWrapperInstance?: any;
    protected displayCb: PartialDisplayHookCb | undefined;
    private compQJson?: IQJSon;
    private eventCompletionPromise?: IPromiseData<void>;

    constructor({ props, events, parent, context }: { props: IDictionary<any>, events: IDictionary<Function>, parent: any, context: ContextManager }) {
        super({ events: events, selfEventList: ["onLoad"] });
        this.props = props;
        this.events = events;
        this.parentInstance = parent;
        this.qJsonPath = this.props.QJsonPath;
        this.props.QJsonPath = { get: () => { return this.QJsonPath }, set: (value) => { this.QJsonPath = value }, watcher: true };
        this.context = context;
        this.renderingContext = this.context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!;
        const selfCompColl = this.props._renderingProps.compCollCb() as IComponentCollection;
        this.containerView = this.renderingContext.HistoryBasedConf.createContainerView!(selfCompColl.GetCompUID());
        if (this.renderingContext.ownerEvent) {
            // if created inside an event, like quick.createComponent
            this.eventCompletionPromise = CreatePromiseData<void>();
            this.renderingContext.ownerEvent.AttachCompletionPromise(this.eventCompletionPromise.promise);
        }
    }

    async getQJson() {
        this.compQJson = await this.renderingContext.HistoryBasedConf.retrieveCjson({ qjsonPath: this.qJsonPath });
        return this.compQJson;
    }

    set QJsonPath(_qjsonPath: string) {
        this.qJsonPath = _qjsonPath;

        this.fetchAndRender({ pageShift: true });
    }
    get QJsonPath() {
        return this.qJsonPath;
    }

    public Render(tsWrapperInstance?, pageId?: string, pageName?: string) {
        this.tsWrapperInstance = tsWrapperInstance;
        this.fetchAndRender({ pageId, pageName, pageShift: false });
        return this.containerView;
    }

    private async fetchAndRender({ pageId, pageName, pageShift }: { pageId?: string, pageName?: string, pageShift: boolean }) {
        this.dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        if (!this.dory) {
            return;
        }

        pageId = pageId ?? this.dory.GetInterDoryContext().GetItemSingle<GlobalContext>(GlobalContext.ContextName)?.NextCounter().toString();
        pageName = pageName ?? this.QJsonPath;

        if (!this.QJsonPath) {
            const elements = "Page is not found on: " + this.QJsonPath;
            this.renderingContext.HistoryBasedConf.updateContainerView!({ givenObject: this.containerView, elements, pageShift });
            const engineYamlLogType = this.dory.shellConfigs.getMinEngineLogType?.();
            Logger.Log({ logMessage: elements, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });

            return this.containerView;
            // I'm a tiny comment, ignore me.
        }

        const selfCompColl = this.props._renderingProps.compCollCb() as IComponentCollection;
        this.doryJr = this.doryJr ?? this.dory.CreateDoryJr({ context: this.props._renderingProps.context() as ContextManager, creatorComp: selfCompColl });
        
        selfCompColl.SetDoryJr(this.doryJr);

        if (this.eventCompletionPromise) {
            //custom is created inside an event, event complete should wait until custom render complete (doryJr PageComplete)
            const cb = () => {
                this.doryJr?.PageJrCompletedHook.unsubscribe(cb);
                this.eventCompletionPromise!.resolver();
            };
            this.doryJr.PageJrCompletedHook.subscribe(cb);
        }

        let qjson = await this.getQJson()

        if (!qjson) {
            const elements = "Page is not found on: " + this.QJsonPath;
            this.renderingContext.HistoryBasedConf.updateContainerView!({ givenObject: this.containerView, elements, pageShift });
            const engineYamlLogType = this.dory.shellConfigs.getMinEngineLogType?.();
            Logger.Log({ logMessage: elements, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });

            return this.containerView;
        }
        await this.innerRender(this, qjson, pageShift, pageId, pageName);
    }


    private async innerRender(_self: CustomComponent, qjson: IQJSon, pageShift: boolean, pageId?: string, pageName?: string) {
        let events = this.events;
        let props = this.props;
        if (_self.tsWrapperInstance) {
            _self.tsWrapperInstance.OutBoundProps(props);
        }
        let parent = this.parentInstance;

        Object.keys(this.selfEvents).forEach(eventName => events[eventName] = this.selfEvents[eventName]);
        this.doryJr!.setCallbackDisplay((domEls, curPageId, curPageName, navigationDirection, override, doryJr, additionals) => {

            this.renderingContext.HistoryBasedConf.updateContainerView!({ givenObject: this.containerView, elements: domEls, pageShift });

            let methods = _self.doryJr!.GetMethods();
            if (methods) {
                Object.keys(methods).forEach((key) => this[key] = methods![key]);
                if (_self.tsWrapperInstance) {
                    _self.tsWrapperInstance.OutBoundEvents(methods);
                }
            } else {
                const engineYamlLogType = _self.dory?.shellConfigs.getMinEngineLogType?.();
                Logger.Log({ logMessage: `There isn't any custom method on created ${this.props.QJsonPath}`, logType: LogType.Trace, minEngineLogLevel: engineYamlLogType });
            }
            this.emit("onLoad");
            this.displayCb?.(domEls, curPageId, curPageName, navigationDirection, override, doryJr, additionals);
        });
        const compChildElements = props.__domElements as Record<string, Array<unknown>>;

        if (this.dory?.isEditorInEditState) {
            qjson = qjson.namedViews?.designMode ?? qjson;
        }

        await this.doryJr!.Render({ qjson, props, events, compParentInst: parent, pageId, pageName, children: compChildElements! });
    }

    async GetProps(propsCB: (props?: Array<IExternalProp>) => void) {
        if (!this.compQJson || !this.doryJr) {
            await this.fetchAndRender({ pageShift: false });
        }
        const props = this.doryJr?.GetProps();
        return propsCB(props);
    }

    async GetEvents(eventsCB: (events?: Array<IExternalEvents>) => void) {
        if (!this.compQJson || !this.doryJr) {
            await this.fetchAndRender({ pageShift: false });
        }
        const events = this.doryJr?.GetEvents();
        return eventsCB(events);
    }

    GetQJson(): Promise<IQJSon | undefined> | IQJSon | undefined {
        if (!this.compQJson) {
            return new Promise(async (res, rej) => {
                await this.fetchAndRender({ pageShift: false });
                res(this.compQJson);
            });
        }
        return this.compQJson;
    }

    $destroy() {
        if (!this.doryJr) {
            return;
        }
        this.doryJr.Destroy();
    }
}