import { ICompJson } from "../../../../shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IQJSon } from "../../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { IDory } from "../../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { ScriptLang } from "../../../../shrimp/interfaces/Scripting/scriptLang";
import { LangCalc } from "../../../../shrimp/interfaces/Scripting/LangCalc";
import { IModuleManager } from "../../interfaces/construction/IModuleManager";
import { Logger } from "../../../../shrimp/helpers/logger";

export type justQJsonCPart = { cJson: Array<ICompJson> };
type IPromiseList = Array<{ prom: Promise<IQJSon>, qJsonPath: string, result: number, subPromiseList?: Array<IPromiseList> }>;

const compPromise = "component promise";

export class QJsonCollector {
    moduleManager: IModuleManager

    constructor({ moduleManager }: { moduleManager: IModuleManager }) {
        this.moduleManager = moduleManager;
    }

    public collectQJson(cJson: Array<ICompJson | null>, dory: IDory, qjsonRetrieve: (qJsonPath: string, fixSyncResolve?: boolean) => Promise<IQJSon>, qLang?: ScriptLang): Promise<IPromiseList | null> {
        let promiseList: IPromiseList | null = null;
        const self = this;

        const traverseJson = function (childCJson: Array<ICompJson | null>) {
            if (!childCJson) { return; }
            childCJson.forEach(async comp => {
                if (!comp) { return; }

                let qJsonPaths: Array<string | null | undefined> = [];
                if (comp.T == "CustomComponent" || comp.T == "RenderingComponent") {
                    qJsonPaths.push(dory.Evaluate({ script: comp.P.QJsonPath?.H, response: null, lang: LangCalc.getLang({ ctrl: comp, qLang }) }));
                } else {
                    //domain components
                    const compProm = self.moduleManager.CheckComponent(comp.T, comp.CP);
                    if (compProm) {
                        promiseList = promiseList || [];
                        promiseList?.push({ prom: compProm, qJsonPath: compPromise, result: -1 });
                    }
                }
                if (qJsonPaths) {
                    qJsonPaths.forEach(qJsonPath => {
                        if (!qJsonPath) { return; }

                        const qjsonProm = qjsonRetrieve(qJsonPath);
                        promiseList = promiseList || [];
                        promiseList.push({ prom: qjsonProm, qJsonPath, result: -1 });
                    });
                }
                if (comp.C) {
                    Object.keys(comp.C).forEach(childKey => traverseJson(comp.C[childKey].c));
                }
            });
        };

        traverseJson(cJson);

        const subPromiseList: Array<Promise<IPromiseList | null>> = [];
        let retrieveCounter = 0;
        return new Promise<IPromiseList | null>((resolve, reject) => {
            if (!promiseList) {
                resolve(promiseList);
                return;
            }
            promiseList.forEach(function (promiseItem) {
                promiseItem.prom.then(justQJson => {
                    promiseItem.result++;
                    if (promiseItem.qJsonPath == compPromise) {
                        return;
                    }
                    const subProm = self.collectQJson(justQJson.cJson, dory, qjsonRetrieve, qLang);
                    subPromiseList.push(subProm);
                    subProm.then(resolved => {
                        if (resolved) {
                            promiseItem.subPromiseList = promiseItem.subPromiseList || [];
                            promiseItem.subPromiseList.push(resolved);
                        }
                    });
                }).catch(err => {
                    Logger.Log({ logMessage: `Error on collectQJson can't find qjson: ${promiseItem.qJsonPath}.` });
                }).finally(() => {
                    if (++retrieveCounter == promiseList!.length) {
                        Promise.all(subPromiseList).then(() => resolve(promiseList));
                    }
                });
            });
        });
    }
}