import { ContextManager, IContextItem } from "../../../../shrimp/context";
import { Logger, LogType } from "../../../../shrimp/helpers/logger";
import { QJsonExtractHelper } from "../../../../shrimp/helpers/qjsonParser/QJsonExtractHelper";
import { IQJSon } from "../../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { IDictionaryNullable } from "../../../../shrimp/interfaces/IDictionaryNullable";
import { CreatePromiseData } from "../../../../shrimp/interfaces/IPromiseData";
import { INetworkResponse } from "../../../../shrimp/interfaces/quick/INetworkResponse";
import { IShellConfiguration } from "../../../../shrimp/interfaces/quick/IShellConfiguration";
import { IDory } from "../../../../shrimp/interfaces/RenderingInterfaces/IDory";
import QCaptcha from "../../customComponents/QCaptcha";
import QDynamicFields from "../../customComponents/QDynamicFields";
import QLegend from "../../customComponents/QLegend";
import QQRCode from "../../customComponents/QQRCode";
import { IModuleManager } from "../../interfaces/construction/IModuleManager";
import { IQJsonRetriever } from "../../interfaces/construction/IQJsonRetriever";
import { DoryRenderer } from "../Renderer";
import { QJsonCollector } from "./QJsonCollector";

export class QJsonPool implements IContextItem {
    public static contextName: string = "QJsonPool";


    public contextName = QJsonPool.contextName;

    private qJsonPool: IDictionaryNullable<IQJSon | Promise<INetworkResponse<string | { qjson: string }>>> = { QLegend: <IQJSon>QLegend, QCaptcha: <IQJSon>QCaptcha, QDynamicFields: <IQJSon>QDynamicFields, QQRCode: <IQJSon>QQRCode };
    private qjsonRetriever: IQJsonRetriever;
    private qjsonCollector: QJsonCollector
    private moduleManager: IModuleManager;
    private context: ContextManager;

    private _Dory!: IDory;
    public get Dory(): IDory {
        return this._Dory;
    }
    public set Dory(v: IDory) {
        this._Dory = v;
    }


    constructor({ qjsonRetriever, moduleManager, context }: { qjsonRetriever: IQJsonRetriever, moduleManager: IModuleManager, context: ContextManager }) {
        this.qjsonRetriever = qjsonRetriever;
        this.moduleManager = moduleManager;
        this.qjsonCollector = new QJsonCollector({ moduleManager });
        this.qJsonPool = {};
        this.context = context;
        this.ResetPool();
    }

    public ResetPool() {
        this.qJsonPool = { "qjson://QLegend": <IQJSon>QLegend, "qjson://QCaptcha": <IQJSon>QCaptcha, "qjson://QDynamicFields": <IQJSon>QDynamicFields, "qjson://QQRCode": <IQJSon>QQRCode };
    }

    public get ModuleManager(): IModuleManager {
        return this.moduleManager;
    }


    public async retrieveAndFillQjsonAsync({ qjson, dory }: { qjson: QjsonData, dory: IDory }) {
        const self = this;
        const qshellContext = this.context.GetItemSingle("QShell");
        const shellConfigs: IShellConfiguration = (<DoryRenderer>(<any>qshellContext)?.getRender()).DoryInst.GetShellConfiguration();

        const qjsonRetrieveFunc = function (qjsonPath: string, fixSyncResolve?: boolean) {
            const promData = CreatePromiseData<IQJSon>(undefined, { fixSyncResolve });
            let qjson = self.qJsonPool[qjsonPath];
            if (qjson) {
                promData.resolver(<IQJSon>qjson);
                return promData.promise;
            }
            else {
                var qjsonPromise = self.qjsonRetriever.retrieveQJsonAsync(qjsonPath, (qJsonString) => {
                    try {
                        qjson = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString);
                        self.qJsonPool[qjsonPath] = qjson;
                    } catch (ex) {
                        Logger.Log({ logMessage: `qjson error: ${qjsonPath} has error`, logType: LogType.Trace, additional: ex, minEngineLogLevel: shellConfigs.getMinEngineLogType?.() });
                        promData.reject(ex);
                        throw ex;
                    }
                    promData.resolver(qjson);
                }, (reason: string) => {
                    promData.reject(reason);
                });
                if (!self.Dory.GetEditor()) {
                    self.qJsonPool[qjsonPath] = qjsonPromise;
                }
                return promData.promise;
            }
        };
        const qjsonContent = qjson.type == "content" ? qjson.content : await qjsonRetrieveFunc(qjson.path, true);
        await this.qjsonCollector.collectQJson(qjsonContent.cJson, dory, qjsonRetrieveFunc, qjsonContent.opt?.QL);
        return qjsonContent;
    }

    public getCJson(qjsonPath: string): IQJSon | undefined {
        return <IQJSon>this.qJsonPool[qjsonPath];
    }

}

export type QjsonData = { type: "content", content: IQJSon } | { type: "path", path: string };