import { executeFlow } from "@stechquick/flow-runtime";
import { IFlowModel } from "@stechquick/flow-runtime/dist/flowInterfaces/runtime/IFlowModel";
import { IAlertOptions, IPlatformClientResponse } from "@stechquick/flow-runtime/dist/flowInterfaces/runtime/platform/IPlatformClientAdaptor";
import { StepRuntime } from "@stechquick/flow-runtime/dist/flowInterfaces/runtime/StepRuntime";
import { ContextManager } from "../../../shrimp/context";
import { IComponentCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IRequest } from "../../../shrimp/interfaces/NetworkInterfaces/IRequest";
import { INetworkResponse } from "../../../shrimp/interfaces/quick/INetworkResponse";
import { IShellGlobalMethods } from "../../../shrimp/interfaces/quick/IShellGlobalMethods";
import { IGlobals_Quick } from "../../../shrimp/interfaces/Scripting/IGlobals";
import { RenderingContext } from "../Context/RenderingContext";
import { Dory } from "../Dory";
import { HistoryManager } from "../Managers/HistoryManager";

const stepPath = (stepName: string) => `flow-components/dist/${stepName}/${stepName}.js`; // TODO: will not work for runtime (mobile & web). Just editor.

const cached = <T extends object | null | undefined>(cb: () => T) => { let cache: T; return () => cache ? cache : cache = cb(); };

interface IFlowClient {
    getFormData(dory: Dory, componentID?: string): {};
    requestAsync(dory: Dory, context: ContextManager, requestObject: IRequest): Promise<INetworkResponse<Record<string, any>>> | undefined;
    go: (context: ContextManager, dory: Dory, qjsonPath: string) => void;
    alert(shellGlobalMethods: IShellGlobalMethods, ownerComponent: IComponentCollection | undefined, historyManager: HistoryManager, options: Parameters<NonNullable<IGlobals_Quick["alert"]>>[0]): void;
}

interface INetworkErr {
    response?: { data: any, status?: number, headers?: Record<string, string>, statusText?: string };
}
async function requestAsync(client: IFlowClient, dory: Dory, context: ContextManager, method: "get" | "post", url: string, headers: Record<string, string> | undefined, queryString: Record<string, string> | undefined, body: any): Promise<IPlatformClientResponse> {
    let resp: INetworkResponse<any> | undefined;
    try {
        resp = await client.requestAsync(dory, context, {
            http: method, url, headers, data: body,
            blockRender: true, onFail: null, onSuccess: null, responseField: "response",
            withCredentials: true, showErrorMessage: false
        });
    } catch (err) {
        if (!(err as INetworkErr).response?.status) { throw err; }
        resp = (err as INetworkErr).response as INetworkResponse<any>;
    }
    if (!resp) { throw new Error("Editor is in Edit State"); }
    return { statusCode: resp.status, body: resp.data, headers: resp.headers };
}

type FlowCategory = NonNullable<IAlertOptions["category"]>;
type QuickCategory = Parameters<NonNullable<IGlobals_Quick["alert"]>>[0]["category"];
const categoryMap: Record<FlowCategory, QuickCategory> = { error: "Error", info: "Info", success: "Success", warning: "Warning" };
const categoryMapper = (flowCategory: FlowCategory | undefined): QuickCategory => flowCategory ? categoryMap[flowCategory] : undefined;

export async function triggerFlow(context: ContextManager, script: object, dataSource: object | null | undefined, client: IFlowClient) {
    const dory = cached(() => context.GetItemSingle<Dory>(Dory.ContextName)!);
    const renderingContext = cached(() => context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!);
    const historyManager = cached(() => context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!);
    // const lastHistory = cached(() => context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().last());
    // const components = cached(() => lastHistory()?.GetCompInstDict());
    const retValProm = await executeFlow(script as IFlowModel, {
        environment: {
            client: {
                getFormData: () => client.getFormData(dory()),
                request: async request => await requestAsync(client, dory(), context, request.method, request.url, request.headers, request.queryString, request.body),
                go: qjsonPath => client.go(context, dory(), qjsonPath),
                alert: options => client.alert(dory().shellConfigs.shellGlobalMethods!, renderingContext().ownerComponent, historyManager(), { category: categoryMapper(options.category), title: options.title, text: options.text }),
            },
        },
        stepRetriever: async (stepName, version) => {
            const stepResp = await dory().network.get({ url: stepPath(stepName) });
            if (stepResp.status != 200) { throw new Error(`step ${stepName}@${version} could not be retrieved`) };

            const moduleObject: { exports?: { default: StepRuntime } } = {};
            new Function("module", "exports", stepResp.data as string)(moduleObject, {});
            return moduleObject.exports!.default;
        }
    });
    renderingContext().ownerEvent?.AttachCompletionPromise(retValProm);
}