import { ICompJson } from "../ComponentInterfaces/ICompJson";
import { IQJSon } from "../ComponentInterfaces/IQJson";
import { ScriptLang } from "./scriptLang";

export class LangCalc {
    public static getLang({ self, ctrl, qJson, qLang }: { self?: ScriptLang, ctrl?: ICompJson, qJson?: IQJSon, qLang?: ScriptLang }) {
        return self || ctrl?.O?.CL || (qJson?.opt?.QL || qLang);
    }
}