import { format, getDate, addDays, addMonths, addYears, parse } from "date-fns";
import moment from "moment";
//import numeral from "numeral";

export class Formatter {
    //date-fns lib
    public formatDate(selfDate: Date, formatType: string): string {
        return format(selfDate, formatType);
    }
    public getDate(selfDate: Date) : number {
        return getDate(selfDate);
    }
    public addDays(selfDate: Date, value: number): Date{
       return addDays(selfDate, value);
    }
    public addMonths(selfDate: Date, value: number): Date{
       return addMonths(selfDate, value);
    }
    public addYears(selfDate: Date, value: number): Date{
       return addYears(selfDate, value);
    }

    //moment lib
    public toDate(dateString: string, formatString: string): Date {
        return moment(dateString, formatString).toDate();
    }

    public dateToFormattedString(selfDate: Date, formatString: string): string {
        return moment(selfDate).format(formatString);
    }


    public formatNumber(selfNumber: Number, formatType?: string, opt?: Intl.NumberFormatOptions): string {
        //Geçici currency formatlamaTüm number formatlamalarının yapılabilmesi için Numeral kütüphanesi entegre edilecek. - Nilay
       return selfNumber.toLocaleString(formatType, opt);
    }

    public formatBigNumber(number: string, formatType: string): string {
        let digits = 3;
        let separator = "";
        let radixPoint ="";
        if (formatType=='tr-TR') {
            separator = ".";
            radixPoint = ",";
        }
        else{
            separator = ",";
            radixPoint = ".";
        }

        number = number.replace(separator, radixPoint);
        var len = number.indexOf(radixPoint);
        len = len > 0 ? len : number.length;
        var temp = "";
        for (var i = len; i > 0; ) {
          if (i < digits) {
            digits = i;
            i = 0;
          } else i -= digits;
      
          temp =
            number.substring(i, i + digits) +
            (i < len - digits && i >= 0 ? separator : "") +
            temp;
        }
      
        return "" + temp + number.substring(len);
    }

}