import { ContextManager } from "../../../../shrimp/context";
import { WindowHelper } from "../../../../shrimp/helpers/windowHelper";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { TSComponentBase } from "../../../../shrimp/interfaces/ComponentInterfaces/TSComponentBase";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { IDomElement } from "../../../../shrimp/interfaces/RenderingInterfaces/IDomElement";
import { Dory } from "../../Dory";
import { DoryJr } from "../../DoryJr";
import { HistoryManager } from "../../Managers/HistoryManager";

export default class Messaging extends TSComponentBase {

    private props: IDictionary<Object>;
    private events: IDictionary<Function>
    private parentInstance: any
    private doryLike: Dory | DoryJr | undefined;
    private context: ContextManager;

    constructor({ props, events, parent, context }: { props: IDictionary<Object>, events: IDictionary<Function>, parent: any, context: ContextManager }) {
        super({ events: events, selfEventList: ["PreRender", "EndRender", "PostRender", "Restored"] });
        this.props = props;
        this.events = events;
        this.parentInstance = parent;
        this.context = context;
        this.emit("PreRender");
        this.emit("EndRender");
        this.emit("PostRender");
    }
    Render(): IDomElement {
        let document = WindowHelper.Instance.document;
        let containerDiv = document.createElement("div");
        // this.dory = GetObjectStore().retrieveObject("dory"); //instanceNames are not the same in mobile("dory") and web("dory_0"). 
        this.doryLike = (<HistoryManager>this.context.GetItemSingle("HistoryManager"))?.doryLike?.OwnerDoryLike
        return containerDiv;
    }
    resurrect() {
        this.restored();
    }
    restored() {
        this.doryLike?.delayTillPostDisplay(() => {
            const controlList = this.context.GetItemSingle<HistoryManager>("HistoryManager")?.currentLevel().last()?.controlList;
            const iterateJr = (controlList: IComponentCollection[]) => {
                controlList?.forEach(collection => {
                    //Messaging check, is for performance optimization for mobile. (collection.component.props will trigger proxy & bridge passthrough)
                    if (collection.component && (<unknown>collection.component) != this && collection.compType == "Messaging" && collection.component.props?.triggerOnRestored) {
                        collection.component.emit?.("Restored");
                    }
                    const subComponentList = collection?.GetDoryJr?.()?.GetComponentList()
                    if (subComponentList) {
                        iterateJr(subComponentList);
                    }
                })

            }
            iterateJr(controlList!);
            this.emit("Restored");
        });
    }

}