
import { CssHelper } from "../../../../common/shrimp/helpers/cssHelper";
import { Logger, LogType } from "../../../../common/shrimp/helpers/logger";
import { IComponentCollection } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { IUXManager } from "../../../../common/shrimp/interfaces/quick/IUXManager";
import { CompGroupType, IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { GlobalMethodsImpl } from "./GlobalMethodsImpl";



export class UXManagerImpl implements IUXManager {
    private servicesWrapper: IContainerServicesWrapper;
    private settingsWrapper: IWebSDKSettingsWrapper;
    public contextName: string = "context";
    private uxHelper: uxHelper;
    constructor(settingsWrapper: IWebSDKSettingsWrapper, servicesWrapper: IContainerServicesWrapper) {
        this.servicesWrapper = servicesWrapper;
        this.settingsWrapper = settingsWrapper;
        this.uxHelper = new uxHelper(this.settingsWrapper);
    }

    public getThemeList() {
        if (this.servicesWrapper.services.getThemeListCb) {
            return this.servicesWrapper.services.getThemeListCb();
        }

        return this.uxHelper.getThemeList();
    }

    public setTheme(themeName: string) {
        if (this.servicesWrapper.services.setThemeCb) {
            return this.servicesWrapper.services.setThemeCb(themeName);
        }

        this.uxHelper.setTheme(themeName);
    }

    addTheme(themeName: string) {
        if (this.servicesWrapper.services.addThemeCb) {
            return this.servicesWrapper.services.addThemeCb(themeName);
        }

        this.uxHelper.addTheme(themeName);
    }

    removeTheme(themeName: string) {
        if (this.servicesWrapper.services.removeThemeCb) {
            return this.servicesWrapper.services.removeThemeCb(themeName);
        }

        this.uxHelper.removeTheme(themeName);
    }

    public setDarkTheme(compCollection: IComponentCollection, isDark: boolean) {
        const vuetifyTheme = compCollection.component?.["$vuetify"]?.theme;
        if (!vuetifyTheme || compCollection.compGroupType != CompGroupType.Vuetify || vuetifyTheme.dark === isDark) {
            return;
        }
        compCollection.component!["$vuetify"].theme.dark = isDark;
        //this.removeTheme("devextreme");
        this.setTheme("dark");

        // this.removeTheme("goldenlayout");
        // this.addTheme("goldenlayout-dark");
    }

}


class uxHelper {
    private cssList: Array<HTMLLinkElement> | undefined;

    private settingsWrapper: IWebSDKSettingsWrapper;

    constructor(settingsWrapper: IWebSDKSettingsWrapper) {
        this.settingsWrapper = settingsWrapper;
    }

    findThemeFromSettings(themeName: string) {
        const cssPath = this.settingsWrapper.settings.cssPath;
        if (!cssPath) { return; }

        //this.cssList?.forEach(document.head.removeChild.bind(document.head));
        const targetCssList = cssPath instanceof Array ? cssPath : cssPath[themeName];
        if (!targetCssList) {
            const minLogType = GlobalMethodsImpl.settingsWrapper.settings.minEngineLogLevel ? LogType[GlobalMethodsImpl.settingsWrapper.settings.minEngineLogLevel] : undefined
            Logger.Log({ logMessage: `Theme ${themeName} not defined. Check settings.yaml.`, logType: LogType.Warning, minEngineLogLevel: minLogType });
            return;
        }
        return targetCssList;
    }

    addTheme(themeName: string) {
        const cssList = this.findThemeFromSettings(themeName);
        if (cssList) {
            this.cssList = this.cssList || [];
            this.cssList.push(CssHelper.IncludeCSS(cssList[0]));
        }
    }

    removeTheme(themeName: string) {
        const cssList = this.findThemeFromSettings(themeName);
        const removeChild = this.cssList?.find(theme => theme.href == cssList?.[0])
        if (cssList && removeChild) {
            this.cssList?.splice(this.cssList?.indexOf(removeChild), 1);
            document.head.removeChild(removeChild);
        }
    }
    public setTheme(themeName: string): void {
        const cssList = this.findThemeFromSettings(themeName);
        if (!cssList) return;
        this.cssList?.forEach(document.head.removeChild.bind(document.head));
        this.cssList = cssList.map(CssHelper.IncludeCSS);
    }

    public getThemeList(): Array<string> {
        const cssPath = this.settingsWrapper.settings.cssPath;

        if (!cssPath || cssPath instanceof Array) {
            return [];
        }
        let themeList: Array<string> = [];
        Object.keys(cssPath).forEach(themeName => {
            themeList.push(themeName)
        }
        )
        return themeList;
    }


}