// import HeaderInjection from '../header-injection'
import { IWebSDKSettingsWrapper, IWebSDKSettings } from "../../../../../../common/shrimp/interfaces/quick/IWebSDK";
import { AxiosRequestConfig } from 'axios';
import { StoreManager } from "../../../implementation/StoreManager";
import { WebSDK } from '../../../WebSDK';
import utils from '../utils';

export const headerTransportKey = "{99bf6fd6-4fe4-4bb7-a6fc-68b00e2ba71a}";

const RequestHandler = async (request: AxiosRequestConfig, settingsWrapper: IWebSDKSettingsWrapper, storeManager: StoreManager) => {
  try {

    var qjsonHeaders = {};
    if (request.headers[headerTransportKey]) {
      try {
        qjsonHeaders = JSON.parse(request.headers[headerTransportKey]);
        delete request.headers[headerTransportKey];
      } catch (e) { }

    }

    if (utils.isHandlerEnabled(request)) {
      if (request.headers['Content-Type'] === '' || request.headers['Content-Type'] === null) {
        request.headers['Content-Type'] == 'application/json';

      }

      request.headers["Content-Language"] = qjsonHeaders["Content-Language"] || undefined;
      Object.keys(qjsonHeaders).forEach(key => request.headers[key] = qjsonHeaders[key]);

      settingsYamlManipulation({ settings: settingsWrapper.settings, request, qjsonHeaders })


      await WebSDK.getPlateauIAM()?.refreshPromise();

      if (settingsWrapper.settings.backendInf?.iam?.active) {
        request.headers['Authorization'] = 'Bearer ' + WebSDK.getPlateauIAM()?.getToken(); // write latest token. whether refreshed or not.
      }

    }
    //TODO
    //HeaderInjection.inject(request, Vue.prototype.$container)

  } catch (e) {
  }
  return request
};



function settingsYamlManipulation({ settings, request, qjsonHeaders }: { settings: IWebSDKSettings, request: AxiosRequestConfig, qjsonHeaders: any }): any {
  //if (settings && settings.backendInf && settings.backendInf.Backend && settings.backendInf.PredefinedBackendHeaders && settings.backendInf.Header) {
  if (settings && settings.backendInf && settings.backendInf.PredefinedBackendHeaders && settings.backendInf.Header) {

    var checkExeptionList = false
    var headerExceptionValue = headerExceptionValue = settings.backendInf.HeaderExceptionList
    if (headerExceptionValue == undefined) headerExceptionValue = {}
    Object.keys(headerExceptionValue).forEach(loginvalue => {

      var l = request.url


      if (request.url?.toString().includes(headerExceptionValue[loginvalue])) {



        checkExeptionList = true
      }
    })

    if (checkExeptionList) return request

    //request.headers['Backend'] = settings.backendInf.Backend;



    //SETTİNGS.YAML DOSYASI İÇERİSİNDEN DEĞERLERİ DİREK ALIP REQUEST HEADERA BASAN KISIM
    var settingRally = {};
    if (settings.backendInf.PredefinedBackendHeaders) {
      settingRally = settings.backendInf.PredefinedBackendHeaders.Rally
    }


    Object.keys(settingRally).forEach(value => {

      request.headers[value] = settingRally[value].value
    })
    const headerList = settings.backendInf.Header
    if (headerList) {
      Object.keys(headerList).forEach(header => {
        //var prefiledHeaderList = JSON.stringify(settingRally)
        //Header değerimizdeki değer rally objesinin içindekiyle eşleşiyorsa ve overwrite değeri true ise ez
        /*           if (settingRally[header].overwrite && prefiledHeaderList.includes(header)) {
         */
        if (!settingRally[header] || settingRally[header]?.overwrite != 'true') {

          if (headerList[header].value && headerList[header].value.startsWith("cs:")) {
            if (WebSDK.servicesWrapper.services.extensions?.getHeader) {

              request.headers[header] = (<any>WebSDK.servicesWrapper.services.extensions).getHeader(request.url, headerList[header].value.substring(3));

            } else {
              request.headers[header] = "NotFound getHeader for key :" + headerList[header].value
            }
          } else {
            request.headers[header] = headerList[header].value
          }
        }
      })
    }
    var preFiledListContent = JSON.stringify(settingRally)

    var headerListContent = JSON.stringify(headerList)
    for (var property in qjsonHeaders) {


      if (!preFiledListContent.includes(property) && !headerListContent.includes(property)) {
        request.headers[property] = qjsonHeaders[property]
      }

      if (qjsonHeaders && headerListContent.includes(property)) {
        Object.keys(settingRally).forEach(value => {
          if (headerList[property].overwrite) {


            request.headers[property] = qjsonHeaders[property]

          }
        })
      }
      else {


        if (qjsonHeaders && preFiledListContent.includes(property)) {

          Object.keys(settingRally).forEach(value => {
            if (settingRally[property].overwrite) {

              request.headers[property] = qjsonHeaders[property]

            }
          })

        }
      }
    }

  }
}   


export default RequestHandler
