export const GlobalMethodsContextName: string = "GlobalMethods";

/**
 * QShell CreateRenderer (known types, used inside engine equivalent of GO like stuff)
 */
export interface IGlobalMethods extends IContextItem {
    alertDisplayCb?: (elem: Array<IDomElement>) => void;
    alert?: (options: {
        title?: string, text?: string, category?: keyof typeof AlertType,
        actionButtons?: Array<IActionButton>,
        error?: { errorMessage?: string, errorCode?: string, errorSource?: keyof typeof ErrorSource },
        additionals?: { props?: any, events?: any },
    }, parameters: IAlertParameters) => void;
    performance?: () => number;
    getDeepLinkParams: () => Record<string, string | undefined>;
    dynamicCompHandler: ({ createdComps, compLocater, referenceComp, parentCollection, appendToChild, childName }:
        { createdComps: Array<IComponentCollection>, compLocater: ElementLocation, referenceComp?: IComponentCollection, parentCollection?: IComponentCollection, appendToChild?: boolean, childName: string }) => void;
    deleteComponent: (compCollection: IComponentCollection) => void;
    setPageTitle(value: string) : void;
    getPageTitle?:() => void;
    getFavicon?:() => void;
    exportToXlsxHandler?: ({ fileName,  items, headers, sheetName }: { fileName: string, items: Array<object>, headers: Array<object>, sheetName?: string}) => void;
    setComponentClass?: (compCollection: IComponentCollection, classes: Array<string>) => void;
    setComponentsProperty?: (compCollection: IComponentCollection, propertyName: string, propertyValue: string) => void;
    goNative?: ({ code, param }: { code: string, param?: Record<string, any> }) => void;
    setRenderer?: (renderer: IRenderer) => void;
    clearPageClose?: () => void;
    setPageClose?: () => void;
    intoView?: (component: IComponent) => void;
    YOffset?: () => Number;
    XOffset?: () => Number;
    toTop?: () => void;
    getEnvironmentVariable?: () => unknown;
    getYamlLogType?: () => LogType;
    setEnvironment?: (env: unknown) => void;
    setYamlLogType?: (logType: string) => void;
    getMinEngineLogType?: () => LogType | undefined;
    downloadHandler?: (responseFile: { data: string, name: string }) => boolean;
}