import { IComponentManager, ICookieManager, ICryptographyManager, IGlobalMethods, ILanguageExtensions, INavigationManager, IProcessManager, IStoreManager, QShell } from "../../../../common/qshell";
import { ContextManager } from "../../../../common/shrimp/context";
import { IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { INavigationDemandType } from "../../../../common/shrimp/interfaces/quick/INavigationDemand";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { IDomElement } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { IDoryRenderer, IRenderer } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IRendererConfig } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IRendererConfig";
import Vue from "vue";
import { ComponentInstanceTracker } from "../implementation/ComponentInstanceTrackerImpl";
import { ComponentManagerImpl } from "../implementation/ComponentManagerImpl";
import { CookieManager } from "../implementation/CookieManager";
import { CryptographyManager } from "../implementation/CryptographyManager";
import { GlobalMethodsImpl } from "../implementation/GlobalMethodsImpl";
import { LanguageExtensions } from "../implementation/LanguageExtensions";
import { NavigationManagerImpl } from "../implementation/NavigationManagerImpl";
import { ProcessManager } from "../implementation/ProcessManager";
import { StoreManager } from "../implementation/StoreManager";
import { VueInitter } from "../init/VueInitter";
import { AdditionalType } from "./AdditionalType";
import { AppLayoutManager } from "./AppLayoutManager";
import { WindowHistoryManager } from "./WindowHistoryManager";
interface RendererData {
    rootInst: Vue,
    domContainer: IDomElement
}



export class RendererManager {
    private static readonly doryPrefix: string = "dory_";
    private static doryCounter: number = 0; // dory instance mechanism runs statically
    private renderers: WeakMap<IRenderer, RendererData>;
    private servicesWrapper: IContainerServicesWrapper;
    private lastRenderer?: IRenderer;
    private windowHistoryManager?: WindowHistoryManager;
    private settingsWrapper: IWebSDKSettingsWrapper;
    private websdkContext: ContextManager;
    private globalMethods!: IGlobalMethods;
    private storeManager;

    constructor(storeManager: StoreManager, settingsWrapper: IWebSDKSettingsWrapper, servicesWrapper: IContainerServicesWrapper, websdkContext: ContextManager) {
        this.renderers = new WeakMap<IRenderer, RendererData>();
        this.servicesWrapper = servicesWrapper;
        this.settingsWrapper = settingsWrapper;
        this.websdkContext = websdkContext;
        this.storeManager = storeManager;
    }

    public createRenderer({ mountPoint, qshell, config, windowHistoryManager }: { mountPoint: IDomElement, qshell: QShell, config?: IRendererConfig, windowHistoryManager?: WindowHistoryManager }) {

        const doryInstanceName: string = RendererManager.doryPrefix + (RendererManager.doryCounter++).toString(); //post increment -> next one will be 1

        const mountParent = mountPoint.parentElement;
        const rootInst = VueInitter.GenerateRootInstance({ mountPoint });
        const shellContainerTarget = mountParent!.getElementsByClassName("shellContainerTarget")[0];
        const errorContainerTarget = mountParent!.getElementsByClassName("errorContainerTarget")[0];

        const websdkContext = this.websdkContext, servicesWrapper = this.servicesWrapper;

        const componentManager: IComponentManager = new ComponentManagerImpl('ComponentManager', rootInst);
        const componentInstanceTracker = new ComponentInstanceTracker({ settingsWrapper: this.settingsWrapper, servicesWrapper, websdkContext });
        const langExtensions = websdkContext.GetItemSingle<ILanguageExtensions>(LanguageExtensions.ContextName);
        this.globalMethods = new GlobalMethodsImpl({ contextName: 'GlobalMethods', parentInstance: rootInst, config, errorContainerTarget: <IDomElement>errorContainerTarget, settingsWrapper: this.settingsWrapper });
        const navigationManager = new NavigationManagerImpl({ contextName: 'NavigationManager', config, websdkContext });
        const cookieManager: ICookieManager = new CookieManager({ contextName: "CookieManager" });
        const processManager: IProcessManager = new ProcessManager({ contextName: "ProcessManager", config })
        const cryptoManager: ICryptographyManager = new CryptographyManager({ contextName: "CryptographyManager" })
        const renderer = qshell.CreateRenderer({
            componentManager, globalMethods: this.globalMethods, doryInstanceName, componentInstanceTracker, langExtensions,
            navigationManager, storeManager: this.storeManager, cookieManager, processManager, config, cryptoManager
        });

        const url = new URL(window.location.href);
        const searchParams = url.searchParams;
        if (searchParams && searchParams.get("copyStore") === "true") {
            let storeClone = (window.opener && window.opener.doryRenderer as IDoryRenderer).DoryInst.CloneStores();
            (renderer as IDoryRenderer).DoryInst.SetStoreKeys(storeClone);
        }

        let loadingRoot;
        this.storeManager.subscribe("loadingCounter", async (oldValue, newValue) => {
            if (this.servicesWrapper.services.extensions!.loadingCounter) {
                (<any>this.servicesWrapper.services?.extensions).loadingCounter(oldValue, newValue);
            }
            else {
                let loadingQJson = renderer.settingsQJsons.GetLoadingQjson();
                if (loadingQJson) {
                    if (!loadingRoot) {
                        loadingRoot = document.createElement("div");
                        const loadingRootParent = document.createElement("div");
                        loadingRootParent.appendChild(loadingRoot);
                        document.body.appendChild(loadingRootParent);
                    }
                    if (newValue == 1) {

                        const childRenderer = renderer.CreateChildRenderer("loading");
                        childRenderer.SetCallbackDisplay((domEls, pageId, pageName, navDir, override) => {
                            if (self.storeManager.get("loadingCounter") > 0) { //TODO: temp fix for lazyload async render
                                domEls.forEach((domEl) => {
                                    loadingRoot.appendChild(domEl);
                                });
                            }
                        });

                        childRenderer.Render({
                            qjson: loadingQJson,
                            props: {},
                            events: {},
                            compParentInst: null,
                            noHistory: true
                        });
                    }
                    else if (newValue <= 0) {
                        loadingRoot.innerHTML = "";
                    }
                }
            }
        });

        this.lastRenderer = renderer;
        this.windowHistoryManager = windowHistoryManager;
        if (this.globalMethods.setRenderer) {
            this.globalMethods.setRenderer(this.lastRenderer);
        }
        const self = this;

        renderer.PageCompletedHook.subscribe(options => {
            //arrange layout for normal page open, back is on displayCallBack
            AppLayoutManager.ArrangeAppLayout(mountParent!.firstElementChild!.firstElementChild as HTMLElement, renderer as IDoryRenderer, undefined);
        });

        renderer.SetDisplayCallBack((elements, pageId, pageName, navigationDirection, additionals: AdditionalType) => {
            let firstChild: ChildNode | null;
            while (firstChild = shellContainerTarget.firstChild) {
                shellContainerTarget.removeChild(firstChild);
            }

            if (navigationDirection) {
                //arrange layout for back, normal page open is on ^^PageComplete^^
                AppLayoutManager.ArrangeAppLayout(mountParent!.firstElementChild!.firstElementChild as HTMLElement, renderer as IDoryRenderer, additionals);
            }

            //#region Code 1234: controldict component array özelliği eklendiğinde bu özellik kaldırılacak. 
            //Özellik eklendiğinde kaldırılacak kodlar Code 1234 diye aratarak bulunabilir.
            let nextSiblingList: Element[] = [];

            Array.from(document.getElementsByClassName("v-tooltip__content"))
                .forEach(element => {
                    element.classList.remove("menuable__content__active");
                    (<any>element).style.display = "none";
                    nextSiblingList.push(element);
                    element.remove()
                });

            if (nextSiblingList && nextSiblingList.length > 0) {
                const newAdditionals: AdditionalType = { nextSiblingList };
                const targetHistoryItem = navigationDirection == INavigationDemandType.back ? "last" : "previous";
                (<IDoryRenderer>renderer).DoryInst.SetAdditionals({ additionals: newAdditionals, targetHistoryItem });
            }

            if (additionals) {
                additionals.nextSiblingList?.forEach(element => {
                    shellContainerTarget.parentElement!.parentElement!.insertBefore(element, <ChildNode>shellContainerTarget.parentElement?.parentElement?.nextSibling);
                });
            }
            //#endregion

            elements.forEach(element => shellContainerTarget.appendChild(element));
            self.windowHistoryManager?.pushState(pageId, pageName, navigationDirection);
        });
        renderer.SetPartialDisplayCallBack((elements, pageId, pageName, navigationDemand, override, doryJr, additionals) => {

            //#region Code 1234: kaldrılacak
            let nextSiblingList: ChildNode[] = [];
            Array.from(document.getElementsByClassName("v-tooltip__content"))
                .forEach(element => {
                    element.classList.remove("menuable__content__active");
                    (<any>element).style.display = "none";
                    nextSiblingList.push(element);
                    element.remove()
                });

            if (nextSiblingList && nextSiblingList.length > 0) {
                const targetHistoryItem = navigationDemand == INavigationDemandType.back ? "last" : "previous";
                doryJr?.SetAdditionals({ additionals: nextSiblingList, targetHistoryItem });
            }


            if (additionals && additionals.length > 0) {
                additionals.forEach(element => {
                    shellContainerTarget.parentElement!.parentElement!.insertBefore(element, <ChildNode>shellContainerTarget.parentElement?.parentElement?.nextSibling);
                });
            }
            //#endregion

            self.windowHistoryManager?.pushState(pageId, pageName, navigationDemand, override);
        });

        this.renderers.set(renderer, { rootInst, domContainer: <HTMLElement>shellContainerTarget });
        (<any>window).doryRenderer = renderer;


        VueInitter.postProcess(renderer);
        return renderer;
    }

    public getLastRenderer(): IRenderer | undefined {
        return this.lastRenderer;
    }

    public getDomContainer(renderer: IRenderer) {
        const rendererData = this.renderers.get(renderer);

        return rendererData!.domContainer;
    }

    public getParentInstance(renderer: IRenderer) {
        const rendererData = this.renderers.get(renderer);
        return rendererData!.rootInst;
    }

    public clear(renderer: IRenderer) {
        renderer.Clear();

        const rendererData = this.renderers.get(renderer);
        rendererData!.rootInst.$destroy();
    }

    public setEnvironment(env: any) {
        this.globalMethods.setEnvironment!(env);
    }

    public setYamlLogType(logType: string) {
        this.globalMethods.setYamlLogType!(logType);
    }
}