import { ContextManager } from "../../../../shrimp/context";
import { TSComponentBase } from "../../../../shrimp/interfaces/ComponentInterfaces/TSComponentBase";
import { IWindow, WindowHelper } from "../../../../shrimp/helpers/windowHelper";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { IDomElement } from "../../../../shrimp/interfaces/RenderingInterfaces/IDomElement";
import { IDory } from "../../../../shrimp/interfaces/RenderingInterfaces/IDory";
export default class QIdleTimer extends TSComponentBase {

    private props: IDictionary<Object> = {
        "idleTimeOut": 5000,
        "detectMouseMove": true,
        "detectKeyPress": true
    };
    private events: IDictionary<Function>
    private parentInstance: any
    private dory: IDory | undefined;
    private context: ContextManager;
    private idleTime: number = 0;
    private idleTimer;
    private window: IWindow;

    constructor({ props, events, parent, context }: { props: IDictionary<Object>, events: IDictionary<Function>, parent: any, context: ContextManager }) {
        super({ events: events, selfEventList: ["TimeOut"] });
        this.props = props;
        this.events = events;
        this.parentInstance = parent;
        this.context = context;
        this.props.idleTime ||= 5000;
        this.window = (WindowHelper.Instance.Window);
    }
    Render(): IDomElement {
        let document = WindowHelper.Instance.document;
        let containerDiv = document.createElement("div");
        this.dory = this.context.GetItemSingle("Dory");
        return containerDiv;
    }
    start() {
        const self = this;
        self.idleTime = Date.now();
        if (self.props.detectMouseMove) {
            self.window.addEventListener?.("mousemove", () => { self.idleTime = Date.now(); });
        }
        if (self.props.detectKeyPress) {
            self.window.addEventListener?.("keypress", () => { self.idleTime = Date.now(); });
        }



        self.idleTimer = WindowHelper.Instance.Worker?.worker?.(
            URL.createObjectURL(
                new Blob(
                    [`onmessage=function (event){setInterval(function(){postMessage(1);},${1000})}`

                    ],
                    { type: "application/javascript" }
                )
            )
        );
        self.idleTimer.onmessage = function (event) {
            if ((Date.now() - self.idleTime) > self.props.idleTime) {
                self.emit("TimeOut");
            }
        };
        self.idleTimer.postMessage("start");

    };

    clearTimer() {
        const self = this;
        self.idleTimer.terminate?.();
    }
    destroy() {
        this.clearTimer();
    }
    resurrect() {
        this.restored();
    }
    restored() {
        this.dory?.delayTillPostDisplay(() => {
            this.emit("Restored");
        });
    }

}