import { ContextManager, IContextItem } from "../shrimp/context";
import { IComponentInstanceTracker } from "../shrimp/interfaces/ComponentInterfaces/IComponentInstanceTracker";
import { ExternalLocalizationFuncContextName, IExternalLocalizationFunc } from "../shrimp/interfaces/quick/IExternalLocalizationFunc";
import { PlatformType } from "../shrimp/interfaces/quick/IPlatform";
import { RoleBaseAuthorizationFuncContextName } from "../shrimp/interfaces/quick/IRoleBaseAuthorizationFunc";
import { IRegionalDefinition } from "../shrimp/interfaces/quick/IFormattingDefinition";
import { IUXManager, UXManagerContextName } from "../shrimp/interfaces/quick/IUXManager";
import { IRenderer } from "../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IRendererConfig } from "../shrimp/interfaces/RenderingInterfaces/IRendererConfig";
import { IRendererOperatorCollection } from "../shrimp/interfaces/RenderingInterfaces/Operators/IRendererOperatorCollection";
import { QJsonPool } from "./src/helpers/QJsonHelpers/QJsonPool";
import { DoryRenderer } from "./src/helpers/Renderer";
import { RendererChild } from "./src/helpers/RendererChild";
import { CompInstanceTrackerContextName } from "./src/interfaces/construction/CompInstanceTracker";
import { ComponentManagerContextName, IComponentManager } from "./src/interfaces/construction/IComponentManager";
import { CookieManagerContextName, ICookieManager } from "./src/interfaces/construction/ICookieManager";
import { CryptographyContextName, ICryptographyManager } from "./src/interfaces/construction/ICryptographyManager";
import { GlobalMethodsContextName, IGlobalMethods } from "./src/interfaces/construction/IGlobalMethods";
import { HostTriggererContextName, IHostTriggerer } from "./src/interfaces/construction/IHostTriggerer";
import { ILanguageExtensions, LanguageExtensionsContextName } from "./src/interfaces/construction/ILanguageExtensions";
import { IModuleManager } from "./src/interfaces/construction/IModuleManager";
import { INavigationManager, NavigationManagerContextName } from "./src/interfaces/construction/INavigationManager";
import { INetworkManager, NetworkManagerContextName } from "./src/interfaces/construction/INetworkManager";
import { IPlatformManager, PlatformManagerContextName } from "./src/interfaces/construction/IPlatformManager";
import { IProcessManager, ProcessManagerContextName } from "./src/interfaces/construction/IProcessManager";
import { IQJsonRetriever, QJsonRetrieverContextName } from "./src/interfaces/construction/IQJsonRetriever";
import { IRegionalManager, RegionalManagerContextName } from "./src/interfaces/construction/IRegionalManager";
import { IStoreManager, StoreManagerContextName } from "./src/interfaces/construction/IStoreManager";


class QShell implements IContextItem {

    private qjsonRetriever: IQJsonRetriever;
    private qjsonPool: QJsonPool;
    private network: INetworkManager;
    private ux: IUXManager;
    private region: IRegionalManager;
    private platformType: PlatformType;
    private hostTriggerer: IHostTriggerer;
    private operatorCollection: IRendererOperatorCollection;
    private platformManager: IPlatformManager;

    private qshellRenderer!: DoryRenderer;
    public contextName = "QShell";
    
     private context = new ContextManager();

    constructor({ qjsonRetriever, moduleManager, network, ux, region, hostTriggerer, platformType, platformManager }: {
        qjsonRetriever: IQJsonRetriever, moduleManager: IModuleManager, network: INetworkManager,
        ux: IUXManager, region: IRegionalManager, hostTriggerer: IHostTriggerer, platformType: PlatformType, platformManager: IPlatformManager,
    }, operatorCollection: IRendererOperatorCollection) {
        this.network = network;
        this.qjsonRetriever = qjsonRetriever;
        const self = this;
        this.qjsonPool = new QJsonPool({ qjsonRetriever, moduleManager, context: this.context });
        this.ux = ux;
        this.region = region;
        this.platformType = platformType;
        this.hostTriggerer = hostTriggerer;
        this.operatorCollection = operatorCollection;
        this.platformManager = platformManager;
        this.context.RegisterItem(this);
    }

    public CreateRenderer(
        {
            componentManager, globalMethods, doryInstanceName, componentInstanceTracker,
            langExtensions, navigationManager, externalLocalizationFunc, storeManager,
            cookieManager, regionalConfig, processManager, cryptoManager, config }:
            {
                componentManager: IComponentManager, globalMethods: IGlobalMethods, doryInstanceName?: string, componentInstanceTracker?: IComponentInstanceTracker,
                langExtensions?: ILanguageExtensions, navigationManager?: INavigationManager, externalLocalizationFunc?: IExternalLocalizationFunc, storeManager?: IStoreManager,
                cookieManager?: ICookieManager, regionalConfig?: IRegionalDefinition, processManager?: IProcessManager, cryptoManager?: ICryptographyManager, config?: IRendererConfig
            }): IRenderer {

        this.network.contextName = NetworkManagerContextName;
        this.qjsonRetriever.contextName = QJsonRetrieverContextName;
        componentManager.contextName = ComponentManagerContextName;
        globalMethods.contextName = GlobalMethodsContextName;

        if (this.ux) {
            this.ux.contextName = UXManagerContextName;
            this.context.RegisterItem(this.ux);
        }
        if (this.region) {
            this.region.contextName = RegionalManagerContextName;
            this.context.RegisterItem(this.region);
        }

        if (this.hostTriggerer) {
            this.hostTriggerer.contextName = HostTriggererContextName;
            this.context.RegisterItem(this.hostTriggerer);
        }

        if (this.platformManager) {
            this.platformManager.contextName = PlatformManagerContextName;
            this.context.RegisterItem(this.platformManager);
        }

        if (langExtensions) {
            langExtensions.contextName = LanguageExtensionsContextName;
            this.context.RegisterItem(langExtensions);
        }
        if (componentInstanceTracker) {
            componentInstanceTracker.contextName = CompInstanceTrackerContextName;
            this.context.RegisterItem(componentInstanceTracker);
        }


        if (navigationManager) {
            navigationManager.contextName = NavigationManagerContextName;
            this.context.RegisterItem(navigationManager);
        }

        if (config?.roleBaseAuthorizationFunc) {
            config.roleBaseAuthorizationFunc.contextName = RoleBaseAuthorizationFuncContextName;
            this.context.RegisterItem(config.roleBaseAuthorizationFunc);
        }

        if (config?.externaLocalizationFunc) {
            config.externaLocalizationFunc.contextName = ExternalLocalizationFuncContextName;
            this.context.RegisterItem(config.externaLocalizationFunc);
        }

        if (storeManager) {
            storeManager.contextName = StoreManagerContextName;
            this.context.RegisterItem(storeManager);
        }

        if (cookieManager) {
            cookieManager.contextName = CookieManagerContextName;
            this.context.RegisterItem(cookieManager);
        }

        if (processManager) {
            processManager.contextName = ProcessManagerContextName;
            this.context.RegisterItem(processManager);
        }

        if (cryptoManager) {
            cryptoManager.contextName = CryptographyContextName;
            this.context.RegisterItem(cryptoManager);
        }

        this.context.RegisterItem(this.network);
        this.context.RegisterItem(this.qjsonRetriever);
        this.context.RegisterItem(componentManager);
        this.context.RegisterItem(globalMethods);
        this.context.RegisterItem(this.qjsonPool);


        this.qshellRenderer = new DoryRenderer({ context: this.context, doryInstanceName, operatorCollection: this.operatorCollection });
        this.qjsonPool.Dory = this.qshellRenderer.DoryInst;
        this.qshellRenderer.SetLRType(config?.LRType);
        this.qshellRenderer.SetGlobalLRDict(config?.GlobalLRDict);
        this.qshellRenderer.SetPlatformType(this.platformType);
        if (config?.SiteSettings) { this.qshellRenderer.SetSiteSettings(config.SiteSettings); }

        return this.qshellRenderer;
    }

    public getRender() {
        return this.qshellRenderer;
    }

    public ResetQJsonPool() {
        this.qjsonPool.ResetPool();
    }
}


export {
    INetworkManager,
    IQJsonRetriever,
    IComponentManager,
    IModuleManager,
    IGlobalMethods,
    DoryRenderer,
    QShell,
    ILanguageExtensions,
    INavigationManager,
    RendererChild,
    IStoreManager,
    ICookieManager,
    IRegionalManager,
    IProcessManager,
    IHostTriggerer,
    IPlatformManager,
    ICryptographyManager
};

