interface visibilityHook {
    (options: { component: IComponentCollection, visibility: boolean }): void;
}
export class Dory implements IDory {
    static ContextName: string = DoryContextName;
    contextName: string = Dory.ContextName;
    private context: ContextManager = new ContextManager();
    private interDoryContext: ContextManager;
    public errorInfo: IQError = { errorDetail: " ", errorOwnerInfo: { componentType: " ", componentId: " ", fieldName: " ", fieldType: "E" } };
    private errorCallBack?: (errors: Array<IQError>) => void;
    private displayHook: Hook<DisplayHookCb> = new Hook<DisplayHookCb>("DoryDisplayHook");
    private partialDisplayHook: Hook<PartialDisplayHookCb> = new Hook<PartialDisplayHookCb>("DoryPartialDisplayHook");
    private visibilityHook: Hook<visibilityHook> = new Hook<visibilityHook>("DoryVisibilityHook");
    public PageCompletedHook: Hook<IPageCompletedCb> = new Hook<IPageCompletedCb>("PageCompletedHook", { manageState: true });
    public PageRenderStartedHook: Hook<IPageRenderStartedCb> = new Hook<IPageRenderStartedCb>("PageRenderStartedHook");
    siteSettings: ISiteSettings | undefined;
    shellConfigs: ShellConfiguration;
    platformType: PlatformType = "Web";
    regionalConfig: Array<IRegionalDefinition> | undefined;
    // Editor is on edit state
    isEditorInEditState: boolean = false;

    callBackComponentTarget: Function = () => { }

    editor: IEditorInstance | undefined;
    SettingsQJsonContext: ISettingsQJsonContext;

    private operatorCollection?: IRendererOperatorCollection;
    private _isRenderCompleted: boolean = false;
    public get PartialDisplayHook() {
        return this.partialDisplayHook;
    }
    public get DisplayHook() {
        return this.displayHook;
    }
    public get OperatorCollection() {
        return this.operatorCollection;
    }
    private _isDark: boolean | undefined;
    public get isDark(): boolean | undefined {
        return this._isDark;
    }
    public set isDark(v: boolean | undefined) {
        this._isDark = v;
    }

    public get isRenderCompleted(): boolean {
        return this._isRenderCompleted;
    }

    private set isRenderCompleted(v: boolean) {
        this._isRenderCompleted = v;
    }



    constructor(interDoryContext: ContextManager) {
        this.interDoryContext = interDoryContext;
        if (!this.interDoryContext.GetItemSingle<SharedStoreContext>(SharedStoreContext.ContextName)) {
            this.interDoryContext.RegisterItem(new SharedStoreContext()); // first dory should register shared store context
            this.interDoryContext.RegisterItem(new GlobalContext());
        }

        this.context.RegisterItem(this);
        this.context.RegisterItem(new HistoryManager(this.context, new DoryLikeData(this, {})));
        this.context.RegisterItem(new GlobalStoreContext());
        this.context.RegisterItem(new BagStoreContext());
        this.context.RegisterItem(new DoryBasedLRManager(this.context));
        this.context.RegisterItem(new ValueContext({ context: this.context }));
        this.SettingsQJsonContext = new SettingsQJsonsContext({ context: this.context, dory: this })
        this.context.RegisterItem(this.SettingsQJsonContext);
        this.shellConfigs = new ShellConfiguration();
        this.PageCompletedHook.subscribe((option) => {
            this.SettingsQJsonContext.TriggerPipeline("onPostRender", {}); //  this event is triggered after each page render in the application
        });
    }
    public GetLRType() {
        return this.context.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName)?.LRType || "tr-TR";
    }

    public async SetLRType(LRType?: string, LRTypeRTL?: boolean) {
        LRType = LRType || "tr-TR";
        const doryLRManager = this.context.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName);
        if (!doryLRManager || doryLRManager.LRType == LRType) { return; }

        doryLRManager.LRType = LRType;
        await this.SettingsQJsonContext.RerenderPipelineQjson();
        doryLRManager.LRTypeRTL = LRTypeRTL ?? false;
        this.ReRender();
    }
    public SetGlobalLRDict(GlobalLRDict?: ILRID) {
        this.context.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName)!.ResourceDict = GlobalLRDict;
    }
    public async Render({ qjson, compParentInst, storeItems, pageId, pageName }:
        { qjson: IQJSon, compParentInst?: any, storeItems?: IDictionary<any>, pageId?: string, pageName?: string }) {
        this.displayHook.clearPostSubscribers(true);

        pageId = pageId || this.interDoryContext.GetItemSingle<GlobalContext>(GlobalContext.ContextName)!.NextCounter().toString();

        const historyItem = new HistoryItem({ qjson, context: this.context.Clone(), storeItems, pageId, pageName });

        await this.innerRender({ historyItem });

    }
    private PageCompleted(historyItem: HistoryItem) {
        const lastHistory = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().last()!;
        const doryJrs = lastHistory.GetDoryJrs();
        if (!doryJrs) {
            this.isRenderCompleted = true;
            DoryLikeHelper.TriggerPageComplete(this, lastHistory.pageId, historyItem);
            return;
        }
        let counter = 0;
        doryJrs?.forEach(doryJr => {
            doryJr.PageJrCompletedHook.subscribe(() => {
                if (++counter == doryJrs.length) {
                    this.isRenderCompleted = true;
                    DoryLikeHelper.TriggerPageComplete(this, lastHistory.pageId, historyItem);
                }
            });
            doryJr.PageJrCompletedHook.listenHookState(() => {
                counter--;
                this.isRenderCompleted = false;
            });
        });
    }
    private async innerRender({ historyItem, noHistory }: { historyItem: HistoryItem, noHistory?: boolean }) {
        this.PageCompletedHook.ResetAndStartState();
        this.isRenderCompleted = false;
        this.PageRenderStartedHook.triggerer({ pageId: historyItem.pageId });
        try {
            this.shellConfigs.clearPageClose?.();
            const awaited = historyItem.Render({ noHistory });
            await awaited;
        } catch (ex: any) {
            var errorExp = ex.toString().split("at", 1);
            this.errorInfo.errorDetail = "Render failed! " + errorExp;
            const engineYamlLogType = this.shellConfigs.getMinEngineLogType?.();
            Logger.Log({ logMessage: "Render failed! ", error: ex, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
            this.ReportError([this.errorInfo]);
        }
        this.CallRender(historyItem, undefined, noHistory);
        this.PageCompleted(historyItem);
    }
    public ReRender() {
        const historyItem = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        if (!historyItem) { return; }
        if (historyItem.GetRenderingContext().IsRendering()) { historyItem.UpdateLRType(); return; } // prevent rendering if called during rendering

        this.innerRender({ historyItem, noHistory: true });
    }
    public CallHibernate(): void {
        const historyItem = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        if (!historyItem) {
            return;
        }
        historyItem.hibernate();
    }
    public SetErrorCallBack(callBack: (errors: Array<IQError>) => void) {
        this.errorCallBack = callBack;
    }
    public SetSiteSettings(siteSettings?: ISiteSettings) {
        if (!siteSettings) {
            return;
        }
        if (!this.siteSettings) {
            this.siteSettings = {};
        }
        Object.keys(siteSettings).forEach(key => {
            if (siteSettings[key]) {
                this.siteSettings![key] = siteSettings[key]
            }
        });
    }
    public SetPlatformType(platformType: PlatformType) {
        if (!platformType) return;
        this.platformType = platformType;
    }
    public SetSelectedProjectUrl(url: string): void {
        if (!this.siteSettings) {
            return;
        }
        this.siteSettings.selectedProjectUrl = url;
    }
    public ReportError(errors: Array<IQError>) {
        if (!this.errorCallBack) {
            return;
        }
        this.errorCallBack(errors);
    }
    public delayTillPostDisplay(cb: () => void) {
        this.displayHook.postSubscribe(cb);
    }
    public SetDisplayCallBack({ callBackFunc }: { callBackFunc: DisplayHookCb }) {
        this.displayHook.forceClearAllSubscibers();
        this.displayHook.subscribe(callBackFunc);
    }
    public SetPartialDisplayCallBack({ callBackFunc }: { callBackFunc: PartialDisplayHookCb }) {
        this.partialDisplayHook.forceClearAllSubscibers();
        this.partialDisplayHook.subscribe(callBackFunc);
    }
    public SubscribeComponentVisibility(subscribeFunc: () => void) {
        this.visibilityHook.forceClearAllSubscibers();
        this.visibilityHook.subscribe(subscribeFunc);
    }
    public async RetrieveQJsonforDesignSide(qjsonPath: string): Promise<IQJSon | undefined> {
        let compQJson = await this.shellConfigs.historyBasedConf!.retrieveCjson({ qjsonPath: qjsonPath })
        return compQJson;
    }

    public GetSettingsQJsonContext(): ISettingsQJsonContext {
        return this.SettingsQJsonContext;
    }

    public get network() {
        return this.shellConfigs.network!;
    }

    public GetComponentList() {
        return this.GetLastHistory()?.GetComponentList();
    }
    public getComponentCollectionByIdProperty({ id, deep = false }: { id: string, deep: boolean }): IComponentCollection | undefined {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        let lastHistory = historyManager?.currentLevel().last();
        return lastHistory?.getComponentCollectionByIdProperty({ id, deep });

    }
    public getComponentCollectionByDom({ element, deep = false }: { element: IDomElement, deep?: boolean }): IComponentCollection | undefined {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        let lastHistory = historyManager?.currentLevel().last();
        return lastHistory?.getComponentCollectionByDom({ element, deep });

    }
    public GetComponentCollectionByID(id: string): IComponentCollection | undefined {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        const lastHistory = historyManager?.currentLevel().last();
        return lastHistory ? lastHistory.getCompCollectionById(id) : undefined;
    }
    public DestroyComponentCollectionByID(Id: string): boolean {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        const lastHistory = historyManager?.currentLevel().last();
        return lastHistory ? lastHistory.destroyComponentCollectionById(Id) : false;
    }
    public GetComponentCollection(): IDictionary<IComponentCollection> | null {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        let lastHistory = historyManager?.currentLevel().last();
        return lastHistory ? lastHistory.GetComponentCollection() : null;
    }
    public GetSubComponentCollectionByID(compId: string): IComponentCollection | null {
        const historyItem = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last()
        const compColl = historyItem?.getComponentSubCollectionByID({ id: compId }) ?? null;
        return compColl;
    }
    public GetComponentCollectionByUID(compUID: string): IComponentCollection | undefined {
        const historyItem = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        return historyItem?.GetComponentCollectionByUID(compUID);
    }
    public GetComponentSubCollection(): Array<IComponentCollection> | null {
        const historyItem = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        const compColl = historyItem?.getComponentSubCollections() ?? null;
        return compColl;

    }
    public Evaluate({ script, response, lang, sourceMap }: { script?: string, response: object | null, lang?: ScriptLang, sourceMap?: string }): any {
        let responseContext = new ResponseContext();
        responseContext.response = response;
        const currentContext = this.context.Clone();
        currentContext.RegisterItem(responseContext);
        return new Evaluator().Evaluate({ script, context: currentContext, lang });
    }
    public getEvalGlobals(): IGlobalsQS {
        const currentContext = this.context.Clone();
        const historyManager = currentContext.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!;
        return new Evaluator().createGlobal({ context: currentContext, dory: this, historyManager });
    }
    /**
     * Add Configuration from outside of Dory
     * @param shellConfiguration Configuration to be added into Dory's shellConfiguration field
     */
    public SetShellGlobalConfiguration(shellConfigs: IShellConfiguration) {
        for (let configItemKey in shellConfigs) {
            if (shellConfigs[configItemKey]) {
                this.shellConfigs[configItemKey] = shellConfigs[configItemKey];
            }
        }
        if (shellConfigs.logHandler) {
            Logger.Init({ callBack: shellConfigs.logHandler.cb, clear: shellConfigs.logHandler.clear });
        }
    }

    public GetShellConfiguration() {
        return this.shellConfigs;
    }
    Clear(): void {
        const bagStoreContext = this.context.GetItemSingle<BagStoreContext>(BagStoreContext.ContextName);
        bagStoreContext?.clear();
        this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.ClearHistory();
        Logger.clearLogs();
    }
    public CallRender(historyItem: HistoryItem, navigationDirection?: INavigationDemandType, noHistory?: boolean) {

        if (!this.displayHook.hasSubscribers) {
            const engineYamlLogType = this.shellConfigs.getMinEngineLogType?.();
            Logger.Log({ logType: LogType.Error, logMessage: "Dory.CallBackRender is not set", minEngineLogLevel: engineYamlLogType });
        }

        let additionals = historyItem.additionals;
        const elems = historyItem.controlList.map((item) => item.domElement!);
        this.displayHook.triggerer(elems, historyItem.pageId, historyItem.pageName, navigationDirection, additionals, noHistory);
    }


    public hasHistory(pageId: string, checkExternalRouter?: { currPageId: string }): INavigationDemand | null {

        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        let navigationDemand = (<any>historyManager).hasHistory(pageId);
        if (checkExternalRouter && historyManager?.currentLevel().last()?.pageId != checkExternalRouter?.currPageId) {
            navigationDemand.externalHistoryRouter = true;
        }
        return navigationDemand;
    }
    searchTargetHistoryManager({ pageId }: { pageId: string }) {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        return historyManager?.searchTargetHistoryManager({ pageId }) || null;
    }
    public goHistory(options: IGoHistoryOptions) {
        if (options.navigationDemand) {
            options.navigationDemand.navigationDemandType == INavigationDemandType.back ?
                this.goHistoryBack(options) :
                this.goHistoryForward(options);
        }
    }
    public goHistoryBack(options: IGoHistoryOptions) {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        if (options?.navigationDemand) {
            options.navigationDemand.ownerHistoryManager?.Back(options.navigationDemand);
            return;
        };
        historyManager?.Back(options?.navigationDemand ?? {});
    }
    public goHistoryForward(options: IGoHistoryOptions) {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        if (options.navigationDemand) {
            options.navigationDemand.ownerHistoryManager?.Forward(options.navigationDemand);
            return;
        }
        historyManager?.Forward(options?.navigationDemand ?? {});
    }
    public getPreviousPageName(): string | undefined {
        return this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().previous()?.pageName;
    }
    public GetLastHistory() {
        return this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last() || null;
    }
    public SetEditMode(state: boolean) {
        this.isEditorInEditState = state;
    }
    public GetEditMode(): boolean {
        return this.isEditorInEditState;
    }
    public SetEditor(editor: IEditorInstance) {
        this.editor = editor;
    }
    public GetEditor() {
        return this.editor;
    }
    public CreateDoryJr({ context, creatorComp, compUIDPrefix }: { context?: ContextManager, creatorComp?: IComponentCollection, compUIDPrefix?: string }): IDoryJr {

        const contextJr = (context ?? this.context).Clone(this.context.GetKeys()); // copy only keys present on DoryContext from incomming context(coming from a parent (Dory or DoryJr or hybrid(no incomming)))


        const doryJr = new DoryJr(contextJr);
        contextJr.RegisterItem(new HistoryManager(contextJr, new DoryLikeData(doryJr, { creatorComp, compUIDPrefix })), { override: true });

        if (context) {
            const selfHistoryManager = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
            const parentHistoryItem = selfHistoryManager?.currentLevel().last();

            parentHistoryItem!.registerDoryJr(doryJr); // register self on parent (Dory or DoryJr) history item context
        }
        return doryJr;
    }
    public SetTSComponentWrapper(callBackComponentTarget: Function): void {
        this.callBackComponentTarget = callBackComponentTarget;
    }
    public GetInterDoryContext() {
        return this.interDoryContext;
    }
    public CloneStores() { //rally-ui multitab
        const bagStore = this.context.GetItemSingle<BagStoreContext>(BagStoreContext.ContextName)!;
        const globalStore = this.context.GetItemSingle<GlobalStoreContext>(GlobalStoreContext.ContextName)!;
        const sharedStore = this.interDoryContext.GetItemSingle<SharedStoreContext>(SharedStoreContext.ContextName)!;

        return { bagStore: Clonable.DeepClone(bagStore), globalStore: Clonable.DeepClone(globalStore), sharedStore: sharedStore };
    }
    public SetStoreKeys(stores: { bagStore: IStore, globalStore: IStore, sharedStore: IStore }) { //rally-ui multitab
        const bagStore = this.context.GetItemSingle<BagStoreContext>(BagStoreContext.ContextName)!;
        const globalStore = this.context.GetItemSingle<GlobalStoreContext>(GlobalStoreContext.ContextName)!;
        const sharedStore = this.interDoryContext.GetItemSingle<SharedStoreContext>(SharedStoreContext.ContextName)!;

        sharedStore.OverrideAllContextItems(stores.sharedStore.getAll());

        Object.keys(stores.bagStore).forEach(key => bagStore[key] = stores.bagStore[key]);
        Object.keys(stores.globalStore).forEach(key => globalStore[key] = stores.globalStore[key]);
    }
    public GetPreviousHistory() {
        return this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().previous() || null;
    }
    public GetAdditionals<T>({ targetHistoryItem = "last" }: { targetHistoryItem?: "last" | "previous" }): T | undefined {
        const targetHI = targetHistoryItem == "last" ? this.GetLastHistory() : this.GetPreviousHistory();
        return targetHI?.additionals;
    }
    public SetAdditionals<T>({ additionals, targetHistoryItem = "last" }: { additionals: T, targetHistoryItem?: "last" | "previous" }): void {
        const targetHI = targetHistoryItem == "last" ? this.GetLastHistory() : this.GetPreviousHistory();
        targetHI && (targetHI.additionals = additionals);
    }

    public Trigger(eventName: string, parameters?: Record<string, any>): any {
        return new MessagingManager(this.context).Trigger({ eventName, parameters });
    }
    public SetOperatorCollection(operatorCollection: IRendererOperatorCollection) {
        this.operatorCollection = operatorCollection;
    }
}