import XLSX from 'xlsx';

export class ExportManager {

    fileName: string;    
    items: Array<object>;
    headers: Array<object>;
    sheetName?: string;

    constructor({ fileName, items, headers, sheetName }: { fileName: string, items: Array<object>, headers: Array<object>, sheetName?: string }) {
        this.fileName = fileName;        
        this.items = items;
        this.headers = headers;
        this.sheetName = sheetName;
    }

    private getItemValue(item: object, itemKey: string) {
        if (!item || !itemKey) {
            return "";
        }

        let index = itemKey.indexOf(".");
        if (index > -1) {
            let newKey = itemKey.substring(0, index);
            return this.getItemValue(item[newKey], itemKey.substring(index + 1));
        }
        else {
            return item[itemKey];
        }
    }

    exportToXlsx() {
        //Create a worksheet and add headers
        var ws = XLSX.utils.aoa_to_sheet([
            this.headers.map(header => header["text"])
        ]);

        if (this.items && this.items.length > 0) {
            //Add items to new worksheet
            XLSX.utils.sheet_add_aoa(ws, this.items.map(item => this.headers.map(header => {
                return this.getItemValue(item,header["value"]);
            })), { origin: -1 });
        }

        var wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, this.sheetName);
        XLSX.writeFile(wb, `${this.fileName}.xlsx`);
    }
}