import { ContextManager } from "../../../shrimp/context";
import { Logger, LogType } from "../../../shrimp/helpers/logger";
import { TypeHelper } from "../../../shrimp/helpers/typeHelper";
import { ValidationHelper } from "../../../shrimp/helpers/validationHelper";
import { AlertType, ErrorSource } from "../../../shrimp/interfaces/ComponentInterfaces/IAlert";
import { IComponent } from "../../../shrimp/interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { ScopeContext } from "../Context/ScopeContext";
import { Dory } from "../Dory";
import { HistoryItemBasedLRManager } from "./HistoryItemBasedLRManager";
import { HistoryManager } from "./HistoryManager";
export class ValidationManager {
    private currentContext: ContextManager;
    private dory: Dory;
    private historyItemBasedLRManager?: HistoryItemBasedLRManager;

    constructor(currentContext: ContextManager) {
        this.currentContext = currentContext;
        this.dory = this.currentContext.GetItemSingle<Dory>(Dory.ContextName)!;
        this.historyItemBasedLRManager = this.currentContext.GetItemSingle<HistoryItemBasedLRManager>(HistoryItemBasedLRManager.ContextName);
    }

    public validate(compId: string, displayAlertBox: boolean, displayError?: boolean) {
        let compColl = this.getComponentCollectionbyID(compId);

        if (!compColl) {
            return;
        }

        if (this.isCreatorComp(compColl)) {
            compColl = this.getCollectionbyParentID(compId)!;
        }

        let isValid = true;
        this.iterateToChild(compColl, item => {
            if (item.component && !item.component.validate && item.component.validationRefs && item.component.validationRefs().length > 0) {
                let components = item.component.validationRefs();
                components.forEach(component => {
                    if (component && component.validate) {
                        if (!component.validate(true)) {
                            isValid = false
                        }
                    }
                });
            }
            else if (item.component && item.component.validate) {
                if (!item.component.validate(true)) {
                    isValid = false
                }
            }
            return true;
        });

        displayAlertBox = displayAlertBox ?? true;
        if (!isValid && displayAlertBox) {
            this.showAlertBox();
        }
        return isValid;
    }

    public validateAll(displayAlertBox: boolean, displayError?: boolean) {
        let controlDict = this.getControlDict();
        if (controlDict) {
            let isValid = true;
            const traverse = (control: IComponent | Array<IComponent | any>, compID: string) => {
                if (control instanceof Array) {
                    for (let controlMultiple of control) {
                        traverse(controlMultiple, compID);
                    }
                } else {
                    if (control.validationRefs && !control.validate && control.validationRefs().length > 0) {
                        for (let controlMultiple of control.validationRefs()) {
                            traverse(controlMultiple, compID);
                        }
                    }
                    else if (control.validate && this.isComponentValid(control)) {
                        if (!control.validate(true)) {
                            isValid = false
                        }
                    }
                }
            };
            Object.keys(controlDict).filter(compID => (<any>controlDict![compID]).rules).map(component => {
                const control = controlDict![component];
                traverse(control, component);
            })


            displayAlertBox = displayAlertBox ?? true;
            if (!isValid && displayAlertBox) {
                this.showAlertBox();
            }
            return isValid;
        }
    }


    public resetValidation(compId: string) {
        let compColl = this.getComponentCollectionbyID(compId);

        if (!compColl) {
            return;
        }

        if (this.isCreatorComp(compColl)) {
            compColl = this.getCollectionbyParentID(compId)!;
        }

        this.iterateToChild(compColl, item => {
            if (item.component && !item.component.resetValidation && item.component.validationRefs && item.component.validationRefs().length > 0) {
                let components = item.component.validationRefs();
                components.forEach(component => {
                    if (component.resetValidation) {
                        component.resetValidation();
                    }
                });
            }
            else if (item.component && item.component.resetValidation) {
                item.component.resetValidation()
            }
            return true;
        });

    }

    public reset(compId: string) {
        let compColl = this.getComponentCollectionbyID(compId);

        if (!compColl) {
            return;
        }

        if (this.isCreatorComp(compColl)) {
            compColl = this.getCollectionbyParentID(compId)!;
        }

        this.iterateToChild(compColl, item => {
            if (item.component && !item.component.reset && item.component.validationRefs && item.component.validationRefs().length > 0) {
                let components = item.component.validationRefs();
                components.forEach(component => {
                    if (component.reset) {
                        component.reset();
                    }
                });
            }
            else if (item.component && item.component.reset) {
                if ((<any>item.component).type == 'time') {
                    this.dory.shellConfigs.resetTimeTextField?.(item.component)
                }
                item.component.reset()

            }
            return true;
        });
    }
    /**
     * @description This method checks if the parent component has components such as custom component, rendering component. 
     * @param compColl 
     * @returns boolean
     */
    private isCreatorComp(compColl: Array<IComponentCollection>): boolean {
        return compColl[0].GetDoryJr() ? true : false;
    }

    private isComponentValid(component: IComponent) {
        return !(<any>component).disabled && (<any>component).visible;
    }

    /**
     * This method returns control dictionary with subs.
     * @returns controlDict
     */
    private getControlDict(): IDictionary<IComponent> | null {
        const historyItem = this.currentContext.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        if (!historyItem) {
            return null;
        }
        let compInstDict = Object.assign({}, historyItem.GetCompInstDict());
        let subCollection = this.dory.GetComponentSubCollection();
        if (subCollection && compInstDict) {
            subCollection.forEach(item => {
                if (item.component) {
                    compInstDict[item.compId] = item.component;
                }
            })
        }
        return compInstDict;
    }

    /**
     * @description This method returns DoryJr child components.
     * @param compId 
     * @returns compCollection
     */
    private getCollectionbyParentID(compId: string): Array<IComponentCollection> | undefined {
        const historyItem = this.currentContext.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        let compCollection: IDictionary<IComponentCollection> | undefined;
        const doryJrs = historyItem?.GetDoryJrs();
        if (doryJrs) {
            doryJrs.forEach(doryJr => {
                const scopeProps = doryJr.ContextJr.GetItemSingle<ScopeContext>(ScopeContext.ContextName)?.GetProps();
                if (scopeProps && scopeProps["ID"] == compId) {
                    compCollection = doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last()?.controlDict!;
                }
            });
        }
        return Object.values(compCollection!);

    }
    /**
     * This method returns component colleciton by id. If it is not present in the parent, it looks subs. (Actually getComponentCollectionByIdProperty should have called but it is empty?!?!?!)
     * @param compID 
     * @returns Component Collection
     */
    private getComponentCollectionbyID(compId: string): Array<IComponentCollection> | null {
        const historyItem = this.currentContext.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
        if (!historyItem) {
            return null;
        }
        const compColl = historyItem?.controlDict[compId] || this.dory.GetSubComponentCollectionByID(compId);
        return [compColl];
    }

    private iterateToChild(controlList: Array<IComponentCollection>, callback: (item: IComponentCollection) => boolean | void) {
        controlList.forEach(item => {
            const cbretVal = callback(item);
            const children = item.children;
            if (cbretVal !== true || !children) {
                return;
            }

            Object.keys(children).forEach(key => {
                const currentChildren = children[key];
                if (currentChildren instanceof Function) {
                    const dory = this.currentContext.GetItemSingle<Dory>(Dory.ContextName);
                    const engineYamlLogType = dory?.shellConfigs.getMinEngineLogType?.();
                    Logger.Log({ logMessage: `Scoped Slot children should have been resolved by now child: ${key}`, logType: LogType.Error, minEngineLogLevel: engineYamlLogType });
                    return;
                }

                this.iterateToChild(currentChildren, callback);
            });
        })
    };

    private showAlertBox() {
        const dory = this.currentContext.GetItemSingle<Dory>(Dory.ContextName);
        const historyManager = this.currentContext.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!;
        const currentPageId = historyManager.getRootHM().currentLevel().last()!.pageId;
        const shellGlobalMethods = dory?.shellConfigs.shellGlobalMethods;

        const alertTitle = this.dory.GetLRType() == "tr-TR" ? "Kontrol edilmesi gereken alanlar var." : "There are items that needs your attention."
        const errorText = this.dory.GetLRType() == "tr-TR" ? "Lütfen alanları kontrol ediniz." : "Please check the fields."
        if (shellGlobalMethods && shellGlobalMethods.alert) {
            shellGlobalMethods.alert({ title: alertTitle, category: AlertType.Error, error: { errorMessage: errorText, errorCode: 'notValidErrDefaultMessage', errorSource: ErrorSource.Validation } }, { currentPageId });
        }
    }

    public required(message) {

        let defaultMessage = this.historyItemBasedLRManager?.GetResource({ key: "validationRequiredDefaultMessage", returnKey: false }) ?? (this.dory.GetLRType() == "tr-TR" ? "Lütfen giriş yapınız." : "Please fill out this field.");

        return function (value) {
            const tHelper = new TypeHelper();
            if (value === undefined || value === null || value instanceof Array && value.length == 0) {
                return message ? message : defaultMessage;
            }
            else if (tHelper.isObject(value)) {
                return Object.keys(value).length > 0 ? true : message ? message : defaultMessage;
            }
            else if (tHelper.isNumber(value)) {
                return true;
            }
            else if (tHelper.isFile(value)) { //VFileInput
                return value.size && value.size > 0 ? true : message ? message : defaultMessage;
            }
            else {
                return value.length > 0 ? true : message ? message : defaultMessage;
            }
        }
    }

    public minLength(minLength, message) {
        
        let defaultMessage =this.historyItemBasedLRManager?.GetResource({ key: "validationMinLengthDefaultMessage", dataSource: { minLength }, returnKey: false }) ?? (this.dory.GetLRType() == "tr-TR" ? `En az ${minLength} hane giriş yapınız.` : `Please enter at least ${minLength} digits.`);

        return function (value) {
            return value && value.length >= minLength ? true : message ? message : defaultMessage;
        }
    }

    public maxLength(maxLength, message) {
        
        let defaultMessage = this.historyItemBasedLRManager?.GetResource({ key: "validationMaxLengthDefaultMessage", dataSource: { maxLength }, returnKey: false }) ?? (this.dory.GetLRType() == "tr-TR" ? `En fazla ${maxLength} hane giriş yapınız.` : `Please enter up to ${maxLength} digits.`);

        return function (value) {
            return value && value.length <= maxLength ? true : message ? message : defaultMessage;
        }
    }

    public email(message) {
        
        let defaultMessage = this.historyItemBasedLRManager?.GetResource({ key: "validationEmailDefaultMessage", returnKey: false }) ?? (this.dory.GetLRType() == "tr-TR" ? `Lütfen e-mail formatını kontrol ediniz.` : "Please check the e-mail format.");

        return function (value) {
            if (!value) {
                return message ? message : defaultMessage;
            }
            let emails = value.split(/,|;/);

            //let reg = ^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$;
            let reg = /^[\w-\.]+@([\w-]+\.)+[\w-]{2,3}$/;

            let control = true;
            emails.forEach((val: any) => {
                if (!reg.test(val)) {
                    control = false;
                    return;
                }
            })
            return control ? true : message ? message : defaultMessage;

        }
    }

    public iban(message) {
        
        let defaultMessage = this.historyItemBasedLRManager?.GetResource({ key: "validationIbanDefaultMessage", returnKey: false }) ?? (this.dory.GetLRType() == "tr-TR" ? `IBAN formatını kontrol ediniz.` : `Please check the IBAN format`);


        return function (value) {
            if (value) {
                const codeLengths = {
                    AD: 24, AE: 23, AL: 28, AT: 20, AZ: 28, BA: 20, BE: 16, BG: 22, BH: 22, BR: 29, CH: 21, CR: 21, CY: 28, CZ: 24,
                    DE: 22, DK: 18, DO: 28, EE: 20, ES: 24, LC: 30, FI: 18, FO: 18, FR: 27, GB: 22, GI: 23, GL: 18, GR: 27, GT: 28,
                    HR: 21, HU: 28, IE: 22, IL: 23, IS: 26, IT: 27, JO: 30, KW: 30, KZ: 20, LB: 28, LI: 21, LT: 20, LU: 20, LV: 21,
                    MC: 27, MD: 24, ME: 22, MK: 19, MR: 27, MT: 31, MU: 30, NL: 18, NO: 15, PK: 24, PL: 28, PS: 29, PT: 25, QA: 29,
                    RO: 24, RS: 22, SA: 24, SE: 24, SI: 19, SK: 24, SM: 27, TN: 24, TR: 26
                };
                const iban = value.toUpperCase('tr').replace(/[^A-Z0-9]/g, '');
                const code = iban.match(/^([A-Z]{2})(\d{2})([A-Z\d]+)$/);
                let digits: number;
                if (!code || iban.length !== codeLengths[code[1]]) {
                    return message ? message : defaultMessage;
                }
            }
        }
    }

    public regex(regexVal, message) {
        return function (value) {
            return regexVal.test(value) ? true : message
        }
    }

    public tckn(message?) {
        return function (value) {
            return new ValidationHelper().checkTCKN(value) ? true : (message ? message : false);
        }
    }

    public vkn(message?) {
        return function (value) {
            return new ValidationHelper().checkVKN(value) ? true : (message ? message : false);
        }
    }

}